<template>
  <div id="app">
    <router-view v-wechat-title="WebSite" v-if="show" />
    <div :is="install" v-if="install"></div>
  </div>
</template>

<script>
console.log(
  "%c%s",
  "color: blue; font-size: 16px;",
  "本程序完美的使用了VUE脚手架\n\n作者邮箱admin@osuu.net 你可以通过 https://osuu.net 了解我们 \n\n人生苦短、必须性感 "
);
import api from "@/api";
import { Dialog } from "vant";
import install from "@/components/install/index";
export default {
  name: "App",
  data() {
    return {
      WebSite: "",
      show: true,
      install:""
    };
  },
  components: {
    install
  },
  mounted() {
    this.$axios({
      url: api.site
    }).then(res => {
      if (res.data.status == 404) {
        this.show = false;
        Dialog.alert({
          message: "您还未安装，是否立即安装?"
        }).then(() => {
          this.install = "install"
        });
      }
      this.WebSite = this.$route.meta.title + " - " + res.data.data.name;
    });

    this._getAuth();
   
  },
  methods: {
    _getAuth() {
      this.$axios({
        url: api.auth
      }).then(res => {
        if (res.data.status == 100) {
          this.show = false;
          Dialog.alert({
            message: res.data.message
          }).then(() => {
            window.location.href = res.data.url;
          });
        }
      });
    }
  }
};
</script>

<style>
.el-message {
  min-width: 340px;
}
</style>



