<template>
  <div>
    <div :is="template"></div>
  </div>
</template>

<script>
import api from "@/api";
import tmp1_mobile from "./template/bf_shop/mobile_goods";
import tmp1_pc from "./template/bf_shop/pc_goods";
export default {
  data() {
    return {
      template: ""
    };
  },
  components: {
    tmp1_mobile,
    tmp1_pc
  },
  mounted() {
    if (document.body.clientWidth < 768) {
      this.template = tmp1_mobile;
    } else {
      this.template = tmp1_pc;
    }
  }
};
</script>

<style scoped>
</style>


