<template>
  <div>
    <div class="content-page conn">
      <div class="content">
        <div class="container">
          <div class="row" style="padding:0 10px">
            <div class="navhead">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/admin/index' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item>模板设置</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12">
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h3 class="panel-title">
                    <font>模板设置</font>
                  </h3>
                </div>
                <div class="panel-body">
                  <div class="col-md-4">
                    <div class="bf_temp">
                      <div class="bf_temp_title">
                        <van-tag type="danger" round>官</van-tag>BF_SHOP官方模板
                        <el-button size="mini" round type="success" class="bf_sy" v-if="template==1">当前在用</el-button>
                        <el-button size="mini" round type="primary" class="bf_sy" v-else @click="addTem(1)">使用</el-button>
                       
                      </div>
                      <div class="bf_temp_img">
                        <img src="../template/bf_shop/bf_shop.png" class="bf_imgs" alt />
                      </div>
                    </div>
                  </div>


                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import { Toast, Dialog } from "vant";
import qs from "querystring";
export default {
  data() {
    return {
      template: "1"
    };
  },
  mounted() {
    this._getTem();
  },
  methods: {
    addTem(num) {
      this.$axios({
        url: api.config,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          template: num
        })
      }).then(res => {
        if (res.data.status == 200) {
          this.$message({
            message: res.data.message,
            type: "success"
          });
          this._getTem();
        } else {
          this.$message.error(res.data.message);
        }
      });
    },

    //获取模板
    _getTem() {
      this.$axios({
        url: api.config,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          all: true
        })
      }).then(res => {
        this.template = res.data.data.template;
      });
    }
  }
};
</script>

<style scoped>
.el-alert {
  margin-bottom: 30px;
}
.el-breadcrumb {
  line-height: 40px;
}
.bf_temp {
  height: 180px;
  border-radius: 10px;
  padding: 0 10px;
  cursor: pointer;
  box-shadow: 1px 1px 5px #ccc;
}
.bf_temp_title {
  height: 40px;
  line-height: 40px;
  font-size: 15px;
  border-bottom: 0.5px solid #ccc;
}
.van-tag {
  height: 20px;
  line-height: 40px;
  margin-right: 5px;
}
.bf_imgs {
  width: 100%;
}
.bf_sy {
  float: right;
  margin-top: 5px;
}
</style>


