<template>
  <div>
    <div class="content-page conn">
      <div class="content">
        <div class="container">
          <div class="row" style="padding:0 10px">
            <div class="navhead">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/admin/index' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item v-if="!id">卡密管理</el-breadcrumb-item>
                <el-breadcrumb-item v-if="id" :to="{ path: '/admin/kmlist' }">卡密管理</el-breadcrumb-item>
                <el-breadcrumb-item v-if="id">{{name}}</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12">
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h3 class="panel-title">
                    <font>卡密管理</font>

                    <el-button
                      type="danger"
                      @click="allDel"
                      size="mini"
                      style="float:right"
                      round
                    >删除已售出卡密</el-button>
                  </h3>
                </div>
                <div class="panel-body">
                  <el-table
                    v-loading="loading"
                    :data="kmList.filter(data => !search || data.km.toLowerCase().includes(search.toLowerCase()))"
                    highlight-current-row
                    style="width: 100%"
                  >
                    <el-table-column type="index" width="40"></el-table-column>
                    <el-table-column label="卡密信息" width="120">
                      <template slot-scope="scope">
                        <span
                          v-if="scope.row.status==1"
                          style="text-decoration:line-through"
                        >{{ scope.row.km}}</span>
                        <span v-else>{{ scope.row.km}}</span>
                      </template>
                    </el-table-column>

                    <el-table-column label="添加时间" width="160">
                      <template slot-scope="scope">
                        <span>{{ scope.row.starttime | date }}</span>
                      </template>
                    </el-table-column>
                    <el-table-column label="售出时间" width="160">
                      <template slot-scope="scope" v-if="scope.row.status==1">
                        <span>{{ scope.row.endtime | date }}</span>
                      </template>
                    </el-table-column>
                    <el-table-column label="买家" width="110">
                      <template slot-scope="scope" v-if="scope.row.status==1">
                        <span>{{ scope.row.user }}</span>
                      </template>
                    </el-table-column>
                    <el-table-column label="商户单号" width="100">
                      <template slot-scope="scope" v-if="scope.row.status==1">
                        <span>{{ scope.row.out_trade_no }}</span>
                      </template>
                    </el-table-column>
                    <el-table-column label="状态" width="100">
                      <template slot-scope="scope">
                        <span
                          v-if="scope.row.status==0"
                          style="color:green"
                        >{{ scope.row.status | type }}</span>
                        <span v-else style="color:red">{{ scope.row.status | type }}</span>
                      </template>
                    </el-table-column>
                    <el-table-column labelalign="right" width="160">
                      <template slot="header" slot-scope="scope">
                        <el-input v-model="search" size="mini" placeholder="输入卡密搜索" />
                      </template>
                      <template slot-scope="scope">
                        <el-button
                          size="mini"
                          type="danger"
                          @click="kmDel(scope.$index, scope.row)"
                        >删除</el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import { Toast, Dialog } from "vant";
import qs from "querystring";
export default {
  data() {
    return {
      kmList: [],
      goods: [],
      loading: true,
      search: "",
      id: "",
      name:""
    };
  },
  filters: {
    //时间戳转日期
    date: function(time) {
      var date = new Date(time * 1000); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate() + " ";
      var h = date.getHours() + ":";
      var m = date.getMinutes() + ":";
      var s = date.getSeconds();
      return Y + M + D + h + m + s;
    },
    //状态
    type: function(state) {
      if (state == 0) {
        return "未出售";
      } else {
        return "已售出";
      }
    }
  },
  mounted() {
    this.id = this.$route.query.id;
    if (this.$route.query.id) {
      this._getKm(this.$route.query.id);
      this.getGoods(this.$route.query.id)
    } else {
      this._getKm();
    }
  },
  methods: {
    //删除已售出卡密
    allDel() {
      Dialog.confirm({
        title: "确认删除",
        message: "删除后无法恢复,是否继续?"
      })
        .then(() => {
          this.$axios({
            url: api.km,
            method: "post",
            data: qs.stringify({
              username: sessionStorage.getItem("adminuser"),
              password: sessionStorage.getItem("adminpass"),
              alldel: true
            })
          }).then(res => {
            if (res.data.status == 200) {
              this.$message({
                message: res.data.message,
                type: "success"
              });
            } else {
              this.$message.error(res.data.message);
            }
            this._getKm();
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    //  删除单个卡密
    kmDel(index, row) {
      Dialog.confirm({
        title: "确认删除",
        message: "删除后无法恢复,是否继续?"
      })
        .then(() => {
          this.$axios({
            url: api.km,
            method: "post",
            data: qs.stringify({
              username: sessionStorage.getItem("adminuser"),
              password: sessionStorage.getItem("adminpass"),
              del: row.id
            })
          }).then(res => {
            if (res.data.status == 200) {
              this.$message({
                message: res.data.message,
                type: "success"
              });
            } else {
              this.$message.error(res.data.message);
            }
            this._getKm();
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    //获取卡密
    _getKm(id) {
      if (!id) {
        this.$axios({
          url: api.km,
          method: "post",
          data: qs.stringify({
            username: sessionStorage.getItem("adminuser"),
            password: sessionStorage.getItem("adminpass"),
            all: true
          })
        }).then(res => {
          this.kmList = res.data.data;
          this.loading = false;
        });
      }else{
          this.$axios({
          url: api.km,
          method: "post",
          data: qs.stringify({
            username: sessionStorage.getItem("adminuser"),
            password: sessionStorage.getItem("adminpass"),
            gid: id
          })
        }).then(res => {
          this.kmList = res.data.data;
          this.loading = false;
        });
      }
    },
    //获取商品
    getGoods(id){
        this.$axios({
        url: api.goods,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          id: id
        })
      }).then(res => {
        this.name = res.data.data.name;
      });
    }
  }
};
</script>


<style scoped>
.el-breadcrumb {
  line-height: 40px;
}
</style>


