<template>
  <div>
    <div class="content-page conn">
      <div class="content">
        <div class="container">
          <div class="row" style="padding:0 10px">
            <div class="navhead">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/admin/index' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item>促销管理</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12">
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h3 class="panel-title">
                    <font>商品促销</font>
                    <el-button
                      type="primary"
                      data-toggle="modal"
                      data-target="#myAdd"
                      size="mini"
                      style="float:right"
                      round
                    >添加促销</el-button>
                  </h3>
                </div>
                <div class="panel-body">
                  <el-table
                    v-loading="loading"
                    :data="proList.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
                    highlight-current-row
                    style="width: 100%"
                  >
                    <el-table-column type="index" width="50"></el-table-column>
                    <el-table-column property="name" label="促销名称" width="160"></el-table-column>
                    <el-table-column property="gName" label="商品名称" width="100"></el-table-column>
                    <el-table-column label="结束时间" width="200">
                      <template slot-scope="scope">
                        <span>{{ scope.row.endtime | date }}</span>
                      </template>
                    </el-table-column>
                    <el-table-column label="促销折扣" width="120">
                      <template slot-scope="scope">
                        <span>{{ scope.row.discount }}折</span>
                      </template>
                    </el-table-column>
                   
                    <el-table-column labelalign="right" width="260">
                      <template slot="header" slot-scope="scope">
                        <el-input v-model="search" size="mini" placeholder="输入名称搜索" />
                      </template>
                      <template slot-scope="scope">
                          <el-button
                          size="mini"
                          type="success"
                          @click="$router.push('/goods/'+scope.row.goodsid+'.html')"
                        >查看商品</el-button>
                        <el-button
                          size="mini"
                          type="danger"
                          @click="proDel(scope.$index, scope.row)"
                        >删除</el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                </div>

                <!-- 添加 -->
                <div
                  class="modal fade"
                  id="myAdd"
                  tabindex="-1"
                  role="dialog"
                  aria-labelledby="myModalLabel"
                >
                  <div class="modal-dialog" role="document">
                    <div class="modal-content">
                      <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                          <span aria-hidden="true">&times;</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">添加促销</h4>
                      </div>
                      <div class="modal-body">
                        <div class="form-group">
                          <label>促销名称:</label>
                          <el-input v-model="name" placeholder="请输入名称"></el-input>
                        </div>

                        <div class="form-group">
                          <label style="display:block">选择商品:</label>
                          <el-select v-model="goodsid" placeholder="请选择商品">
                            <el-option
                              v-for="item in goodsList"
                              :key="item.id"
                              :label="item.name"
                              :value="item.id"
                            ></el-option>
                          </el-select>
                        </div>
                        <div class="form-group">
                          <label>结束时间:</label>
                          <div class="block">
                            <div class="block">
                              <el-date-picker
                                v-model="endtime"
                                type="datetime"
                                value-format="timestamp"
                                placeholder="选择日期时间"
                              ></el-date-picker>
                            </div>
                          </div>
                        </div>
                        <div class="form-group">
                          <label style="display:block">促销折扣:</label>
                          <el-input-number v-model="discount" :min="1" :max="10"></el-input-number>
                          <p style="color:#ccc">* 例如:8表示在原价基础上打8折</p>
                        </div>
                      </div>
                      <div class="modal-footer text-center">
                        <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
                        <button type="button" class="btn btn-primary" @click="addPro">确认</button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import { Toast, Dialog } from "vant";
import qs from "querystring";
export default {
  data() {
    return {
      goodsList: [],
      proList: [],

      search: "",

      name: "",
      goodsid: "",
      endtime: "",
      discount: "",
      loading: true
    };
  },
  filters: {
    //时间戳转日期
    date: function(time) {
      var date = new Date(time * 1000); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate() + " ";
      var h = date.getHours() + ":";
      var m = date.getMinutes() + ":";
      var s = date.getSeconds();
      return Y + M + D + h + m + s;
    },
    //状态
    shop: function(id) {
      this.goodsList.map(function(item,index){
          if(item.id == id)
            return item.name
        })
    }
  },
  mounted() {
    this._getGoods();
    this._getPro();
  },
  methods: {
    //  删除
    proDel(index, row) {
      Dialog.confirm({
        title: "确认删除",
        message: "删除后无法恢复,是否继续?"
      })
        .then(() => {
          this.$axios({
            url: api.promotion,
            method: "post",
            data: qs.stringify({
              username: sessionStorage.getItem("adminuser"),
              password: sessionStorage.getItem("adminpass"),
              del: row.id
            })
          }).then(res => {
            if (res.data.status == 200) {
              this.$message({
                message: res.data.message,
                type: "success"
              });
            } else {
              this.$message.error(res.data.message);
            }
            this._getPro();
          });
        })
        .catch(() => {
          // on cancel
        });
    },

    //获取全部促销
    _getPro() {
      this.$axios({
        url: api.promotion,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          all: true
        })
      }).then(res => {
        this.proList = res.data.data;
        this.loading = false;
      });
    },
    //获取全部商品
    _getGoods() {
      this.$axios({
        url: api.promotion,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          allgoods: true
        })
      }).then(res => {
        this.goodsList = res.data.data;
        this.loading = false;
      });
    },
    //添加促销
    addPro() {
        console.log(this.endtime)
      if (!this.name) {
        this.$message.error("促销名称不能为空");
      } else if (!this.goodsid) {
        this.$message.error("促销商品不能为空");
      } else if (!this.endtime) {
        this.$message.error("结束时间不能为空");
      } else {
        this.$axios({
          url: api.promotion,
          method: "post",
          data: qs.stringify({
            username: sessionStorage.getItem("adminuser"),
            password: sessionStorage.getItem("adminpass"),
            add: this.name,
            goodsid: this.goodsid,
            endtime: this.endtime/1000,
            discount: this.discount
          })
        }).then(res => {
          if (res.data.status == 200) {
            this.$message({
              message: res.data.message,
              type: "success"
            });
            this.name = "";
            this.goodsid = "";
            this.endtime = "";
            this.discount = "";
            $("#myAdd").modal("hide");
            this._getPro();
          } else {
            this.$message.error(res.data.message);
          }
        });
      }
    }
  }
};
</script>


<style scoped>
.el-breadcrumb {
  line-height: 40px;
}
</style>


