<template>
  <div id="bf_pc">
    <div class="bf_topbar">
      <div class="bf_main">
        <div class="bf_topbar_left">欢迎使用{{data.name}}</div>
        <div class="bf_topbar_right">
          <a @click="$router.push('/query')">订单查询</a>
          <a @click="kf">联系客服</a>
        </div>
      </div>
    </div>
    <!-- LOGI、搜索 -->
    <div class="bf_midbar">
      <div class="bf_main">
        <div class="bf_midbar_left">
          <a href="/">
            <img :src="`/upload/${data.logo}`" alt />
          </a>
        </div>
        <div class="bf_midbar_right">
          <div class="bf_search">
            <el-input placeholder="请输入商品名称" v-model="goodsquery" class="input-with-select">
              <el-button slot="append" icon="el-icon-search" @click="gQuery"></el-button>
            </el-input>
            <ul>
              <li style="border-left:0;padding-left:0" @click="$router.push('/')">全部商品</li>
              <li
                v-for="(item,index) in allList"
                :key="item.id"
                v-if="index<data.rec"
                @click="gList(item.id)"
              >{{item.name}}</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <!-- 分类导航 -->
    <div class="bf_navbar">
      <div class="bf_main">
        <ul>
          <li>
            <a @click="$router.push('/')">全部</a>
          </li>
          <li v-for="item in typeList" :key="item.id">
            <a @click="$router.push('/?typeId='+item.id)">{{item.name}}</a>
          </li>
        </ul>
      </div>
    </div>

    <div class="bf_goodsbox">
      <div class="bf_main">
        <div class="bf_goods_left">
          <div class="bf_cuxiao" v-if="cuxiao">
            <div class="bf_tempbg">
              <div class="bf_cuxiaosm">距结束</div>

              <span :endTime="endTime" :endText="endText" id="timer">
                <slot>{{content}}</slot>
              </span>
              <div class="bf_daoshou">
                <em style="font-size:14px">到手价 ￥</em>
                {{cuxiao_price}}
              </div>
            </div>
          </div>
          <img
            :src="`/upload/${goods.img}`"
            class="bf_goods_img"
            alt
          />
        </div>
        <div class="bf_goods_right">
          <p class="bf_goods_name">{{goods.name}}</p>
          <div class="bf_goods_price">
            <div class="dt">价格</div>
            <div class="dd price" :class="cuxiao?'zhekou':''">
              <em style="font-size:12px">￥</em>
              {{goods.price}}
            </div>
            <div class="dt">促销</div>
            <div class="dd" v-if="cuxiao">
              该商品正在参加
              <span style="color:blue">{{pro.name}}</span>
              活动，
              <span style="color:blue">{{pro.endtime | date}}</span>
              前购买可享受
              <span style="color:blue">{{pro.discount}}</span>
              折，预计到手价
              <span style="color:red">{{cuxiao_price}}</span>元
            </div>
            <div class="dd" v-else>无</div>
          </div>
          <div class="bf_goods_nav">
            <div class="dt">服务</div>
            <div class="dd" v-if="goods.sm1 || goods.sm2 || goods.sm3">
              <el-tag v-if="goods.sm1" size="mini">{{goods.sm1}}</el-tag>
              <el-tag v-if="goods.sm2" type="success" size="mini">{{goods.sm2}}</el-tag>
              <el-tag v-if="goods.sm3" type="warning" size="mini">{{goods.sm3}}</el-tag>
            </div>
            <div class="dd" v-else>
              <el-tag size="mini">自动发货</el-tag>
            </div>
          </div>
          <div class="bf_goods_nav">
            <div class="dt">库存</div>
            <div class="dd" v-if="data.stockno==2">{{stock}}</div>
            <div class="dd" v-else>{{stock | fter}}</div>
          </div>
          <div class="bf_goods_nav">
            <div class="dt">销量</div>
            <div class="dd">{{goods.sales}}</div>
          </div>
          <div class="bf_goods_nav">
            <div class="dt">数量</div>
            <div class="dd">
              <el-input-number v-model="num" size="mini" :min="1" label="购买数量"></el-input-number>
            <em style="font-size:12px;color:red">{{goods.minbuy}}件起购，限购{{goods.maxbuy}}件</em>
            </div>
          </div>
          <div class="bf_goods_nav">
            <div class="dt">QQ</div>
            <div class="dd">
              <el-input size="small" placeholder="请输入联系方式或QQ号" v-model="qq"></el-input>
            </div>
          </div>
          <div class="bf_goods_nav" style="margin-top:30px">
            <div class="dt"></div>
            <div class="dd">
              <el-button type="primary" @click="setPay('alipay')" v-if="pay.alipay != 0">支付宝支付</el-button>
              <el-button type="success" @click="setPay('wxpay')" v-if="pay.wxpay != 0">微信支付</el-button>

              <el-button type="warning" @click="setPay('qqpay')" v-if="pay.qqpay != 0">QQ支付</el-button>
            </div>
          </div>
        </div>
        <div class="bf_goods_bottom">
          <div class="bf_bottom_title">商品介绍</div>
          <div class="bg_content" v-html="goods.info"></div>
        </div>
      </div>
    </div>
    <div class="bf_pay">
      <el-dialog title="订单确认" :visible.sync="dialogVisible" width="30%" :before-close="handleClose">
        <div class="goBuy">
          <div class="pay_list">订单编号 : {{this.orderInfo.out_trade_no}}</div>
          <div class="pay_list">商品名称 : {{this.orderInfo.gName}}</div>
          <div class="pay_list">商品单价 : {{this.goods.price}}</div>
          <div class="pay_list">购买数量 : {{this.num}}</div>
          <div class="pay_list">联系方式 : {{this.qq}}</div>
          <div class="pay_list">实付金额 : {{this.orderInfo.zfPrice}}</div>
          <div class="pay_list">付款方式 : {{this.orderInfo.sm}}</div>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消 支 付</el-button>
          <el-button type="primary" @click="onConfirm">确 定 支 付</el-button>
        </span>
      </el-dialog>
    </div>
    <div class="bf_footer">
      <div class="bf_footer_cont">
        <div v-html="data.link"></div>
        <div v-html="data.copy" class="bf_copy"></div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import qs from "querystring";
//时间戳转日期
function date(time) {
  var date = new Date(time * 1000); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
  var Y = date.getFullYear() + "";
  var M =
    (date.getMonth() + 1 < 10
      ? "" + (date.getMonth() + 1)
      : date.getMonth() + 1) + "";
  var D = date.getDate() + "";
  var h = date.getHours() + ":";
  var m = date.getMinutes() + ":";
  var s = date.getSeconds();
  return M + "月" + D + "日" + h + m + s;
}
export default {
  data() {
    return {
        dialogVisible: false,
      goodsquery: "",
      data: {},
      allList: [],
      typeList: [],
      goodsid: "",
      goods: {},
      num: "",
      stock: "",
      pay: {},
      pro: [],
      cuxiao: false,
      cuxiao_price: "",
      content: "",
      qq: "",
      orderInfo: {
        out_trade_no: "",
        gName: "",
        price: "",
        zfPrice: "",
        gType: "",
        sm: "",
      }
    };
  },
  props: {
    endTime: {
      type: String,
      default: ""
    },
    endText: {
      type: String,
      default: "活动已结束"
    }
  },
  filters: {
    //时间戳转日期
    date: function(time) {
      var date = new Date(time * 1000); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate() + " ";
      var h = date.getHours() + ":";
      var m = date.getMinutes() + ":";
      var s = date.getSeconds();
      return Y + M + D + h + m + s;
    },
    //库存
    fter: function(num) {
      if (num == 0) {
        return "空";
      } else if (num <= 5) {
        return "少量";
      } else if (num <= 10) {
        return "一般";
      } else if (num <= 30) {
        return "很多";
      } else {
        return "充足";
      }
    }
  },
  mounted() {
    this._getSet();
    this._getType();
    this.goodsid = this.$route.params.id;
    this._getGoods(this.$route.params.id);
  },
  methods: {
    gList(id){
      window.location.href = '/goods/'+id+'.html'
    },
       //确认支付
    onConfirm() {
      window.location.href = api.api +"/includes/pay/submit.php?type=" +
        this.orderInfo.gType +
        "&out_trade_no=" +
        this.orderInfo.out_trade_no +
        "&name=" +
        this.orderInfo.gName +
        "&money=" +
        this.orderInfo.zfPrice +
        "&goodsid=" +
        this.goods.id +
        "&num=" +
        this.num+"&qq="+this.qq;

      this.orderInfo = {};
      this.dialogVisible = false;
     
    },
    //订单确认
    setPay(type) {
      let reg = /^[0-9]{6,11}$/;
      let qq = reg.test(this.qq);
      if (!this.num) {
        this.$alert("请输入购买数量", "温馨提示", {
          confirmButtonText: "确定",
          callback: action => {}
        });
      } else if (this.num < this.goods.minbuy) {
        this.$alert("此商品最低购买" + this.goods.minbuy + "件", "温馨提示", {
          confirmButtonText: "确定",
          callback: action => {}
        });
      } else if (this.num > this.goods.maxbuy) {
        this.$alert("此商品最多购买" + this.goods.maxbuy + "件", "温馨提示", {
          confirmButtonText: "确定",
          callback: action => {}
        });
      } else if (this.num > this.stock) {
        this.$alert("库存不足,请减少购买数量", "温馨提示", {
          confirmButtonText: "确定",
          callback: action => {}
        });
      } else if (!this.qq) {
        this.$alert("请填写联系方式", "温馨提示", {
          confirmButtonText: "确定",
          callback: action => {}
        });
      } else if (!qq) {
        this.$alert("请填写正确的联系方式", "温馨提示", {
          confirmButtonText: "确定",
          callback: action => {}
        });
      } else {
        let dates = new Date().getTime();
        this.orderInfo.out_trade_no = this.order(dates);
        this.orderInfo.gName = this.goods.name;
        this.orderInfo.price = this.goods.price;
        this.orderInfo.gType = type;
        if (this.cuxiao) {
          //计算金额
          this.orderInfo.zfPrice = this.cuxiao_price * this.num;
        } else {
          this.orderInfo.zfPrice = this.goods.price * this.num;
        }
        if (type == "alipay") {
          //支付说明
          this.orderInfo.sm = "支付宝";
        } else if (type == "wxpay") {
          this.orderInfo.sm = "微信支付";
        } else {
          this.orderInfo.sm = "QQ支付";
        }
        this.dialogVisible = true;
      }
    },
    //获取商品
    _getGoods(id) {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          goodsid: id
        })
      }).then(res => {
        if (res.data.data == "") {
          this.$confirm("此商品不存在或已下架", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning"
          })
            .then(() => {
              this.$router.push("/");
            })
            .catch(() => {
              this.$router.push("/");
            });
        }
        this.goods = res.data.data;
        this.num = res.data.data.minbuy;
        this._getStock(id); //获取库存
      });
    },
    //获取库存
    _getStock(id) {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          stock: id
        })
      }).then(res => {
        this.stock = res.data.data;
        //获取支付方式
        this._getPayType();
        //获取折扣
        this._getPro(id);
      });
    },
    //获取折扣
    _getPro(id) {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          pro: id
        })
      }).then(res => {
        this.pro = res.data.data;
        if (res.data.data) {
          var timestamp = Date.parse(new Date());
          if (res.data.data.endtime > timestamp / 1000) {
            this.cuxiao = true;
            this.cuxiao_price = (
              (res.data.data.discount / 10) *
              this.goods.price
            ).toFixed(2);
            this.countdowm(res.data.data.endtime);

            //弹窗提示
            this.$notify({
              title: "促销说明",
              dangerouslyUseHTMLString: true,
              duration: 8000,
              offset: 50,
              message:
                '该商品正在参加<span style="color:red">' +
                res.data.data.name +
                '</span>活动 <span style="color:red">' +
                date(res.data.data.endtime) +
                "</span>前购买可享受" +
                res.data.data.discount +
                '折优惠，预计到手价<span style="color:red">' +
                this.cuxiao_price +
                "元</span>"
            });
          }
        }
      });
    },
    //获取支付方式
    _getPayType() {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          pay: true
        })
      }).then(res => {
        this.pay = res.data.data;
      });
    },
    //商品搜索
    gQuery() {
      if (!this.goodsquery) {
        this.$message.error("搜索内容不能为空");
      } else {
        this.$router.push("/?goods=" + this.goodsquery);
      }
    },
    //联系客服
    kf() {
      this.$confirm("客服QQ: " + this.data.kf + " 点击确定立即联系", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "success"
      })
        .then(() => {
          window.location.href =
            "http://wpa.qq.com/msgrd?v=3&uin=" +
            this.data.kf +
            "&site=qq&menu=yes";
        })
        .catch(() => {});
    },
    //获取商品分类
    _getType() {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          type: true
        })
      }).then(res => {
        let compare = function(obj1, obj2) {
          if (obj1.weight > obj2.weight) {
            return -1;
          } else if (obj1.weight < obj2.weight) {
            return 1;
          } else {
            return 0;
          }
        };
        this.typeList = res.data.data.sort(compare);
      });
    },
    //获取全部商品
    _getAllGoods() {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          goods: true
        })
      }).then(res => {
        let compare = function(obj1, obj2) {
          if (obj1.weight > obj2.weight) {
            return -1;
          } else if (obj1.weight < obj2.weight) {
            return 1;
          } else {
            return 0;
          }
        };
        this.goodsList = res.data.data;
        this.allList = res.data.data;
      });
    },
    //获取部分设置
    _getSet() {
      this.$axios({
        url: api.site
      })
        .then(res => {
          this.data = res.data.data;
          //获取商品
          this._getAllGoods();
        })
        .catch(err => {});
    },
    //生成订单号
    order(timestamp) {
      //时间
      var date = new Date(timestamp); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      var Y = date.getFullYear();
      var M =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var D = date.getDate();
      var h = date.getHours();
      var m = date.getMinutes();
      var s = date.getSeconds();
      //后四位随机数
      let str = "0123456789";
      let num = "";
      for (let i = 0; i < 4; i++) {
        num += str[parseInt(Math.random() * str.length)];
      }
      return Y + M + D + h + m + s + num;
    },
    //计算时间
    countdowm(timestamp) {
      let self = this;
      let timer = setInterval(function() {
        let nowTime = new Date();
        let endTime = new Date(timestamp * 1000);
        let t = endTime.getTime() - nowTime.getTime();
        if (t > 0) {
          let day = Math.floor(t / 86400000);
          let hour = Math.floor((t / 3600000) % 24);
          let min = Math.floor((t / 60000) % 60);
          let sec = Math.floor((t / 1000) % 60);
          hour = hour < 10 ? "0" + hour : hour;
          min = min < 10 ? "0" + min : min;
          sec = sec < 10 ? "0" + sec : sec;
          let format = "";
          if (day > 0) {
            format = `${day}:${hour}:${min}:${sec}`;
          }
          if (day <= 0 && hour > 0) {
            format = `${hour}:${min}:${sec}`;
          }
          if (day <= 0 && hour <= 0) {
            format = `${min}:${sec}`;
          }
          self.content = format;
        } else {
          clearInterval(timer);
          self.content = self.endText;
          self._callback();
        }
      }, 1000);
    },
    _callback() {
      if (this.callback && this.callback instanceof Function) {
        this.callback(...this);
      }
    }
  }
};
</script>

<style lang="less" scoped>
@import '../../../assets/css/reset.css';
#bf_pc {
    background: #fff;
  .bf_topbar {
    width: 100%;
    height: 40px;
    position: relative;
    background: #f2f2f2;
    -webkit-transform: translate3d(0, 0, 0);
    border-bottom: 1px solid #e5e5e5;
    line-height: 40px;
    color: #999;
    font-size: 12px;
    .bf_main {
      margin: 0 auto;
      width: 100%;
      max-width: 1230px;
      .bf_topbar_left {
        float: left;
      }
      .bf_topbar_right {
        float: right;
        a {
          color: #999;
          cursor: pointer;
        }
      }
    }
  }
  .bf_midbar {
    height: 130px;
    background-color: #fff;
    .bf_main {
      margin: 0 auto;
      width: 100%;
      max-width: 1230px;
      .bf_midbar_left {
        float: left;
        height: 130px;
        width: 300px;
        img {
          width: 100%;
          height: 100%;
          object-fit: scale-down;
        }
      }
      .bf_midbar_right {
        float: right;
        max-width: 100%;
        width: 625px;
        .bf_search {
          width: 100%;
          margin: 31px auto;
          ul {
            width: 625px;
            height: 8px;
            overflow: hidden;
            list-style: none;
            padding: 10px 0;
            li {
              float: left;
              display: inline-block;
              border-left: 1px solid #ccc;
              line-height: 1.1;
              padding: 0 12px;
              font-size: 16px;
              color: #888;
              cursor: pointer;
            }
          }
        }
      }
    }
  }
  .bf_navbar {
    background-color: #ff4500;
    height: 40px;
    .bf_main {
      margin: 0 auto;
      width: 100%;
      max-width: 1230px;
      overflow: hidden;
      ul {
        width: 1230px;
        overflow: hidden;
        list-style: none;
        li:hover {
          background: #0066ff;
          border-bottom: 1px solid #0066ff;
        }
        li {
          cursor: pointer;
          float: left;
          a {
            display: block;
            font-size: 16px;
            padding: 0 10px;
            font-weight: 700;
            line-height: 38px;
            color: #fff;
          }
        }
      }
    }
  }

  .bf_goodsbox {
    overflow: hidden;
    .bf_main {
      margin: 0 auto;
      padding: 20px 15px;
      width: 100%;
      max-width: 1230px;
      overflow: hidden;
      .bf_goods_left {
        float: left;
        width: 420px;
        position: relative;
        .bf_cuxiao {
          position: absolute;
          width: 100%;
          bottom: 0px;
          height: 80px;
          // background: rgba(53, 47, 47, 0.8);
          background: linear-gradient(to right, #5769f9, #d658fe);
          .bf_tempbg {
            position: relative;
            height: 80px;
            #timer {
              position: absolute;
              left: 10px;
              bottom: 10px;
              color: #fff;
              font-size: 22px;
            }
            .bf_cuxiaosm {
              position: absolute;
              top: 10px;
              left: 10px;
              color: #fff;
              font-size: 18px;
            }
            .bf_daoshou {
              position: absolute;
              right: 20px;
              bottom: 10px;
              font-size: 38px;
              color: red;
            }
          }
        }
        .bf_goods_img {
          width: 100%;
          height: 420px;
          display: block;
          border: solid 1px #eee;
          border-radius: 2px;
        }
      }
      .bf_goods_right {
        float: right;
        width: 760px;
        padding-right: 10px;
        .bf_goods_name {
          font-size: 26px;
          margin: 10px 0;
        }
        .bf_goods_price {
          color: #999;
          background-color: #f4f4f4;
          font-size: 16px;
          padding: 10px;
          margin-bottom: 5px;
          border-radius: 2px;
          margin: 5px 0;
          .dt {
            float: left;
            padding-left: 10px;
            font-family: simsun;
            color: #999;
            line-height: 30px;
          }
          .dd {
            line-height: 30px;
            margin-left: 70px;
          }
          .price {
            color: red;
            font-size: 22px;
            text-align: left;
          }
          .zhekou {
            color: #999;
          }
        }
        .bf_goods_nav {
          margin: 10px 5px;
          font-size: 15px;
          .dt {
            float: left;
            padding-left: 10px;
            font-family: simsun;
            color: #999;
            line-height: 30px;
          }
          .dd {
            line-height: 30px;
            margin-left: 70px;
          }
        }
      }
      .bf_goods_bottom {
        float: left;
        width: 100%;
        margin-top: 20px;
        border: 1px solid #dfdfdf;
        .bf_bottom_title {
          width: 98%;
          border-bottom: 1px solid #dfdfdf;
          padding: 10px 1%;
          font-size: 14px;
        }
        .bg_content {
          padding: 10px;
        }
      }
    }
  }
  .bf_pay {
    .goBuy {
      padding: 10px 20px;
      .pay_list {
        height: 30px;
        line-height: 30px;
      }
    }
  }
  .bf_footer {
    margin-top: 40px;
    border-top: solid 2px #ff4500;
    background-color: #fff;
    padding: 20px 0;
    .bf_footer_cont {
      text-align: center;
      font-size: 12px;
      width: 100%;
      max-width: 1230px;
      margin: 0 auto;
      .bf_copy {
        margin-top: 10px;
      }
    }
  }
}
</style>


