<template>
  <div>
    <div class="content-page conn">
      <div class="content">
        <div class="container">
          <div class="row" style="padding:0 10px">
            <div class="navhead">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/admin/index' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item>分类管理</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12">
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h3 class="panel-title">
                    <font>商品分类</font>
                    <el-button
                      type="primary"
                      data-toggle="modal"
                      data-target="#myAdd"
                      size="mini"
                      style="float:right"
                      round
                    >添加分类</el-button>
                  </h3>
                </div>
                <div class="panel-body">
                  <el-table v-loading="loading"
                    :data="typeList.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
                    highlight-current-row
                    style="width: 100%"
                  >
                    <el-table-column type="index" width="50"></el-table-column>
                    <el-table-column property="name" label="分类名称" width="180"></el-table-column>
                    <el-table-column label="添加时间" width="180">
                      <template slot-scope="scope">
                        <span>{{ scope.row.starttime | date }}</span>
                      </template>
                    </el-table-column>
                    <el-table-column property="weight" label="分类权重" width="130"></el-table-column>

                    <el-table-column label="状态" width="120">
                      <template slot-scope="scope">
                        <span
                          v-if="scope.row.status==1"
                          style="color:green"
                        >{{ scope.row.status | type }}</span>
                        <span v-else style="color:red">{{ scope.row.status | type }}</span>
                      </template>
                    </el-table-column>
                    <el-table-column labelalign="right" width="220">
                      <template slot="header" slot-scope="scope">
                        <el-input v-model="search" size="mini" placeholder="输入名称搜索" />
                      </template>
                      <template slot-scope="scope">
                        <el-button
                          size="mini"
                          type="success"
                          @click="$router.push('/admin/goods?type_id='+scope.row.id)"
                        >商品</el-button>
                        <el-button
                          size="mini"
                          type="primary"
                          @click="edit(scope.$index, scope.row)"
                        >编辑</el-button>
                        <el-button
                          size="mini"
                          type="danger"
                          @click="del(scope.$index, scope.row)"
                        >删除</el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                </div>

                <!-- 添加 -->
                <div
                  class="modal fade"
                  id="myAdd"
                  tabindex="-1"
                  role="dialog"
                  aria-labelledby="myModalLabel"
                >
                  <div class="modal-dialog" role="document">
                    <div class="modal-content">
                      <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                          <span aria-hidden="true">&times;</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">添加分类</h4>
                      </div>
                      <div class="modal-body">
                        <div class="form-group">
                          <label>分类名称:</label>
                          <el-input v-model="name" placeholder="请输入名称"></el-input>
                        </div>

                        <div class="form-group">
                          <label style="display:block">分类权重(值越大排序越靠前)</label>
                          <el-input-number v-model="weight" :min="0"></el-input-number>
                        </div>
                        <div class="form-group">
                          <label>是否启用:</label>
                          <el-switch
                            v-model="status"
                            active-color="#13ce66"
                            inactive-color="#ccc"
                            active-value="1"
                            inactive-value="0"
                          ></el-switch>
                        </div>
                      </div>
                      <div class="modal-footer text-center">
                        <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
                        <button type="button" class="btn btn-primary" @click="addType">确认</button>
                      </div>
                    </div>
                  </div>
                </div>

                <!-- 编辑 -->
                <div
                  class="modal fade"
                  id="myEdit"
                  tabindex="-1"
                  role="dialog"
                  aria-labelledby="myModalLabel"
                >
                  <div class="modal-dialog" role="document">
                    <div class="modal-content">
                      <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                          <span aria-hidden="true">&times;</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">编辑分类</h4>
                      </div>
                      <div class="modal-body">
                        <div class="form-group">
                          <label>分类名称:</label>
                          <el-input v-model="typeInfo.name" placeholder="请输入名称"></el-input>
                        </div>
                        <div class="form-group">
                          <label>添加时间:</label>
                          <el-input disabled :value="typeInfo.starttime | date"></el-input>
                        </div>
                        <div class="form-group">
                          <label style="display:block">分类权重(值越大排序越靠前)</label>
                          <el-input-number v-model="typeInfo.weight" :min="0"></el-input-number>
                        </div>
                        <div class="form-group">
                          <label>是否启用:</label>
                          <el-switch
                            v-model="typeInfo.status"
                            active-color="#13ce66"
                            inactive-color="#ccc"
                            active-value="1"
                            inactive-value="0"
                          ></el-switch>
                        </div>
                      </div>
                      <div class="modal-footer text-center">
                        <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
                        <button type="button" class="btn btn-primary" @click="setType">确认</button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import { Toast, Dialog } from "vant";
import qs from "querystring";
export default {
  data() {
    return {
      typeList: [],
      typeInfo: [],
      search: "",
      name: "",
      weight: "",
      status: "",
      loading:true
    };
  },
  filters: {
    //时间戳转日期
    date: function(time) {
      var date = new Date(time * 1000); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate() + " ";
      var h = date.getHours() + ":";
      var m = date.getMinutes() + ":";
      var s = date.getSeconds();
      return Y + M + D + h + m + s;
    },
    //状态
    type: function(state) {
      if (state == 0) {
        return "未启用";
      } else {
        return "正常";
      }
    }
  },
  mounted() {
    this._getType();
  },
  methods: {
    //提交编辑
    setType() {
      if (!this.typeInfo.name) {
        this.$message.error("分类名字不能为空");
      } else {
        this.$axios({
          url: api.type,
          method: "post",
          data: qs.stringify({
            username: sessionStorage.getItem("adminuser"),
            password: sessionStorage.getItem("adminpass"),
            edit: this.typeInfo.id,
            name: this.typeInfo.name,
            weight: this.typeInfo.weight,
            status: this.typeInfo.status
          })
        }).then(res => {
          if (res.data.status == 200) {
            this.$message({
              message: res.data.message,
              type: "success"
            });
          } else {
            this.$message.error(res.data.message);
          }
          $("#myEdit").modal("hide");
          this._getType();
        });
      }
    },
    // 获取单个分类信息
    edit(index, row) {
      this.$axios({
        url: api.type,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          id: row.id
        })
      }).then(res => {
        this.typeInfo = res.data.data;
        $("#myEdit").modal("show");
      });
    },
    //  删除单个分类
    del(index, row) {
      Dialog.confirm({
        title: "确认删除",
        message: "将同时删除此分类下的商品,是否继续?"
      })
        .then(() => {
          this.$axios({
            url: api.type,
            method: "post",
            data: qs.stringify({
              username: sessionStorage.getItem("adminuser"),
              password: sessionStorage.getItem("adminpass"),
              del: row.id
            })
          }).then(res => {
            if (res.data.status == 200) {
              this.$message({
                message: res.data.message,
                type: "success"
              });
            } else {
              this.$message.error(res.data.message);
            }
            this._getType();
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    //获取全部分类
    _getType() {
      this.$axios({
        url: api.type,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          all: true
        })
      }).then(res => {
        let compare = function(obj1, obj2) {
          if (obj1.weight > obj2.weight) {
            return -1;
          } else if (obj1.weight < obj2.weight) {
            return 1;
          } else {
            return 0;
          }
        };

        this.typeList = res.data.data.sort(compare);
        this.loading = false
      });
    },
    //添加分类
    addType() {
      if (!this.name) {
        this.$message.error("分类名字不能为空");
      } else {
        this.$axios({
          url: api.type,
          method: "post",
          data: qs.stringify({
            username: sessionStorage.getItem("adminuser"),
            password: sessionStorage.getItem("adminpass"),
            add: this.name,
            weight: this.weight,
            status: this.status
          })
        }).then(res => {
          if (res.data.status == 200) {
            this.$message({
              message: res.data.message,
              type: "success"
            });
            this.name = "";
            this.weight = "";
            this.status = "";
            $("#myAdd").modal("hide");
            this._getType();
          } else {
            this.$message.error(res.data.message);
          }
        });
      }
    }
  }
};
</script>


<style scoped>
.el-breadcrumb {
  line-height: 40px;
}
</style>


