<template>
  <div>
    <div class="content-page conn">
      <div class="content">
        <div class="container">
          <div class="row" style="padding:0 10px">
            <div class="navhead">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/admin/index' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item>全局设置</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12">
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h3 class="panel-title">
                    <font>全局设置</font>
                  </h3>
                </div>
                <div class="panel-body">
                  <el-form :model="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="网站名称">
                      <el-input v-model="ruleForm.name"></el-input>
                    </el-form-item>
                    <el-form-item label="首页弹窗公告">
                      <el-input type="textarea" :rows="3" v-model="ruleForm.gg"></el-input>
                      <p style="color:#ccc">* 为空则不显示公告,支持html代码</p>
                    </el-form-item>
                    <el-form-item label="查询页面公告">
                      <el-input type="textarea" :rows="3" v-model="ruleForm.querygg"></el-input>
                      <p style="color:#ccc">* 为空则不显示公告,仅支持纯文本</p>
                    </el-form-item>
                     <el-form-item label="PC首页公告">
                      <el-input type="textarea" :rows="3" v-model="ruleForm.pcgg"></el-input>
                      <p style="color:#ccc">* 不可为空,支持html代码</p>
                    </el-form-item>
                    <el-form-item label="网站底部">
                      <el-input
                        type="textarea"
                        :rows="3"
                        v-model="ruleForm.copy"
                        placeholder="支持html代码"
                      ></el-input>
                    </el-form-item>
                    <el-form-item label="友情链接">
                      <el-input
                        type="textarea"
                        :rows="3"
                        v-model="ruleForm.link"
                        placeholder="支持html代码"
                      ></el-input>
                    </el-form-item>
                    <el-form-item label="网站QQ客服">
                      <el-input v-model="ruleForm.kf" placeholder="请直接输入QQ号"></el-input>
                    </el-form-item>
                    <el-form-item label="图片上传Key">
                      <el-input v-model="ruleForm.upkey" style="width:60%"></el-input>
                      <el-button type="primary" plain class="roundBtn" @click="roundkey(1)">随机生成</el-button>
                      <p style="color:#ccc">* 防止接口被盗用请随机生成</p>
                    </el-form-item>
                    <el-form-item label="邮件发送key">
                      <el-input v-model="ruleForm.monit" style="width:60%"></el-input>
                      <el-button type="primary" plain class="roundBtn" @click="roundkey(2)">随机生成</el-button>
                      <p style="color:#ccc">* 防止接口被盗用请随机生成</p>
                    </el-form-item>
                    <el-form-item label="推荐商品数量" prop="day">
                      <el-input-number v-model="ruleForm.rec" :min="2" :max="8" :step="2"></el-input-number>
                      <p style="color:#ccc">* 推荐商品由商品权重决定</p>
                    </el-form-item>
                    <el-form-item label="库存显示">
                      <el-select v-model="ruleForm.stockno" placeholder="请选择">
                        <el-option label="范围库存" value="1"></el-option>
                        <el-option label="详细库存" value="2"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-alert title="以下选项设置后请进行邮箱配置" type="warning"></el-alert>

                    <el-form-item label="邮箱发卡">
                      <el-switch
                        v-model="ruleForm.emailfk"
                        active-color="#13ce66"
                        inactive-color="#ccc"
                        active-value="1"
                        inactive-value="0"
                      ></el-switch>
                      <p style="color:#ccc">* 开启后如果客户填写联系方式为QQ时则自动发卡到QQ邮箱</p>
                    </el-form-item>

                    <el-form-item label="库存不足预警">
                      <el-switch
                        v-model="ruleForm.early"
                        active-color="#13ce66"
                        inactive-color="#ccc"
                        active-value="1"
                        inactive-value="0"
                      ></el-switch>
                      <p style="color:#ccc">* 开启后库存卡密不足会给你发邮件提醒</p>
                    </el-form-item>
                    <el-form-item label="发件预警额度" v-if="ruleForm.early==1">
                      <el-input-number v-model="ruleForm.earlylim" :min="0" :max="100" :step="1"></el-input-number>
                      <p style="color:#ccc">* 剩余卡密不足多少时发件提醒</p>
                    </el-form-item>
                    <el-form-item label="商品出售提醒">
                      <el-switch
                        v-model="ruleForm.sold"
                        active-color="#13ce66"
                        inactive-color="#ccc"
                        active-value="1"
                        inactive-value="0"
                      ></el-switch>
                      <p style="color:#ccc">* 开启后客户购买后会给你发邮件提醒</p>
                    </el-form-item>

                    <el-form-item>
                      <el-button type="primary" @click="edit">确认修改</el-button>
                    </el-form-item>
                  </el-form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import { Toast, Dialog } from "vant";
import qs from "querystring";
export default {
  data() {
    return {
      ruleForm: {
        name: "",
        monit: "",
        upkey: "",
        gg: "",
        pcgg:"",
        querygg: "",
        kf: "",
        rec: "",
        copy: "",
        link: "",
        stockno: "",
        emailfk: "",
        early: "",
        earlylim: "",
        sold: ""
      }
    };
  },
  mounted() {
    this._getMap();
  },
  methods: {
    _getMap() {
      this.$axios({
        url: api.config,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          all: true
        })
      }).then(res => {
        this.ruleForm = res.data.data;
      });
    },
    edit() {
      this.$axios({
        url: api.config,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          edit: this.ruleForm.name,
          monit: this.ruleForm.monit,
          upkey: this.ruleForm.upkey,
          gg: this.ruleForm.gg,
          pcgg: this.ruleForm.pcgg,
          querygg: this.ruleForm.querygg,
          kf: this.ruleForm.kf,
          rec: this.ruleForm.rec,
          copy: this.ruleForm.copy,
          link: this.ruleForm.link,
          stockno: this.ruleForm.stockno,
          emailfk: this.ruleForm.emailfk,
          early: this.ruleForm.early,
          earlylim: this.ruleForm.earlylim,
          sold: this.ruleForm.sold
        })
      }).then(res => {
        if (res.data.status == 200) {
          this.$message({
            message: res.data.message,
            type: "success"
          });
        } else {
          this.$message.error(res.data.message);
        }

        this._getMap();
      });
    },
    //生成随机key
    roundkey(num) {
      if (num == 1) {
        this.ruleForm.upkey = Math.ceil(Math.random() * 99999999);
      } else if (num == 2) {
        this.ruleForm.monit = Math.ceil(Math.random() * 99999999);
      }
    }
  }
};
</script>

<style scoped>
.roundBtn {
  padding: 12px 8px;
}
.el-alert {
  margin-bottom: 30px;
}
.el-breadcrumb {
  line-height: 40px;
}
</style>


