let url1 = window.location.href.split('/',3)
let api = url1[0]+'//'+url1[2]
let url2 = url1[2]


const login = api + '/includes/api/login_api.php'   //登录接口
const site = api + '/includes/api/site_api.php'   //网站信息接口
const type = api + '/includes/api/type_api.php'   //分类接口
const goods = api + '/includes/api/goods_api.php'   //商品接口
const upload = api + '/includes/api/upload_api.php'   //商品接口
const km = api + '/includes/api/km_api.php'   //卡密接口
const config = api + '/includes/api/config_api.php'   //设置接口
const promotion = api + '/includes/api/promotion_api.php'   //促销接口
const swiper = api + '/includes/api/swiper_api.php'   //轮播接口
const pay = api + '/includes/api/pay_api.php'   //支付接口
const email = api + '/includes/api/email_api.php'   //邮件接口
const send = api + '/includes/api/send_api.php'   //发件接口
const order = api + '/includes/api/order_api.php'   //订单接口

const update = 'http://auth.mall.osuu.net/bf.php'   //更新接口
const auth = 'http://faka.h234.cn/auth.php?url=' + url2  //授权接口



module.exports = {
    login,
    site,
    type,
    goods,
    upload,
    km,
    config,
    promotion,
    swiper,
    pay,
    email,
    send,
    order,
    update,
    auth,
    url2,
    api
}


