<template>
  <div>
    <div class="content-page conn">
      <div class="content">
        <div class="container">
          <div class="row" style="padding:0 10px">
            <div class="navhead">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/admin/index' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item>LOGO设置</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12">
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h3 class="panel-title">
                    <font>LOGO设置</font>
                  </h3>
                </div>
                <div class="panel-body">
                  <el-upload
                    class="upload-demo"
                    :action="action"
                    :data="data"
                    :on-error="imgError"
                    :on-success="imgSuccess"
                    :before-remove="beforeRemove"
                    multiple
                    :limit="1"
                    :on-exceed="handleExceed"
                    :file-list="fileList"
                  >
                    <el-button size="small" type="primary">点击上传</el-button>
                  </el-upload>
                  <el-button type="success" style="margin-top:20px" @click="addTem">提交修改</el-button>

                  <div class="bf_logo">
                    <div class="bf_logotitle">当前LOGO</div>
                    <img
                      :src="`/upload/${logo}`"
                      class="bf_logoimg"
                      alt
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import { Toast, Dialog } from "vant";
import qs from "querystring";
export default {
  data() {
    return {
      data: { key: "" },
      fileList: [],
      action: "",
      imgs: "",
      logo: ""
    };
  },
  mounted() {
    this.action = api.upload;
    this._getTem();
  },
  methods: {
    //上传失败
    imgError(err, file, fileList) {
      this.$message.error(err);
    },
    imgSuccess(response, file, fileList) {
      if (response.status == 200) {
        this.$message({
          message: "上传成功，请提交修改保存",
          type: "success"
        });
        this.imgs = response.data;
      } else {
        this.$message.error(response.message);
        this.fileList = [];
      }
    },

    handleExceed(files, fileList) {
      this.$message.error("上传数量超过限制");
    },
    beforeRemove(file, fileList) {
      return this.$message({
        message: "删除成功",
        type: "success"
      });
    },

    addTem() {
      if (!this.imgs) {
       this.$message.error('请先上传');
      }else{
           this.$axios({
          url: api.config,
          method: "post",
          data: qs.stringify({
            username: sessionStorage.getItem("adminuser"),
            password: sessionStorage.getItem("adminpass"),
            logo: this.imgs
          })
        }).then(res => {
          if (res.data.status == 200) {
            this.$message({
              message: res.data.message,
              type: "success"
            });
            this._getTem();
          } else {
            this.$message.error(res.data.message);
          }
        });
      }
    },

    //获取key
    _getTem() {
      this.$axios({
        url: api.config,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          all: true
        })
      }).then(res => {
        this.data.key = res.data.data.upkey;
        this.logo = res.data.data.logo;
      });
    }
  }
};
</script>

<style scoped>
.el-alert {
  margin-bottom: 30px;
}
.el-breadcrumb {
  line-height: 40px;
}
.bf_logo {
  margin-top: 20px;
  border-radius: 10px;
  font-size: 15px;
  padding: 10px;
  border: 0.4px solid #ccc;
}
.bf_logotitle {
  height: 30px;
  line-height: 30px;
  border-bottom: 0.4px solid #ccc;
}
.bf_logoimg {
  margin-top: 10px;
  width: 100%;
}
</style>


