<template>
  <div>
    <div class="content-page conn">
      <div class="content">
        <div class="container">
          <div class="row" style="padding:0 10px">
            <div class="navhead">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/admin/index' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item>添加商品</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12">
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h3 class="panel-title">
                    <font>添加商品</font>
                  </h3>
                </div>
                <div class="panel-body">
                  <el-form :model="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="商品分类">
                      <el-select v-model="ruleForm.type_id" placeholder="请选择">
                        <el-option
                          v-for="item in typeList"
                          :key="item.id"
                          :label="item.name"
                          :value="item.id"
                        ></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="商品名称" prop="name">
                      <el-input v-model="ruleForm.name"></el-input>
                    </el-form-item>
                    <el-form-item label="商品图片" prop="img">
                      <el-upload
                        class="upload-demo"
                        :action="action"
                        :data="data"
                        :on-error="imgError"
                        :on-success="imgSuccess"
                        :before-remove="beforeRemove"
                        multiple
                        :limit="1"
                        :on-exceed="handleExceed"
                        :file-list="fileList"
                      >
                        <el-button size="small" type="primary">点击上传</el-button>
                      </el-upload>
                    </el-form-item>

                    <el-form-item label="商品价格" prop="price">
                      <el-input-number v-model="ruleForm.price" :min="0"></el-input-number>
                    </el-form-item>
                    <el-form-item label="起购数量" prop="minbuy">
                      <el-input-number v-model="ruleForm.minbuy" :min="1"></el-input-number>
                      <p style="color:#ccc;font-size:12px">* 买家一次最少购买份数</p>
                    </el-form-item>
                    <el-form-item label="限购数量" prop="maxbuy">
                      <el-input-number v-model="ruleForm.maxbuy" :min="1" :max="9999"></el-input-number>
                      <p style="color:#ccc;font-size:12px">* 买家一次最大购买份数</p>
                    </el-form-item>
                    <el-form-item label="商品权重" prop="weight">
                      <el-input-number v-model="ruleForm.weight" :min="0"></el-input-number>
                      <p style="color:#ccc;font-size:12px">* 值越大排序越靠前</p>
                    </el-form-item>
                    <el-form-item label="初始销量" prop="sales">
                      <el-input-number v-model="ruleForm.sales" :min="0"></el-input-number>
                    </el-form-item>

                    <el-form-item label="服务说明" prop="sm">
                      <el-select
                        v-model="ruleForm.sm"
                        multiple
                        filterable
                        allow-create
                        default-first-option
                        placeholder="请选择商品说明"
                      >
                        <el-option
                          v-for="item in options"
                          :key="item.value"
                          :label="item.label"
                          :value="item.value"
                        ></el-option>
                      </el-select>
                       <p style="color:#ccc;font-size:12px">* 可自行输入,添加商品后不可修改</p>
                    </el-form-item>
                    <el-form-item label="商品介绍" prop="info">
                      <el-input
                        type="textarea"
                        :rows="5"
                        placeholder="请输入内容,支持html"
                        v-model="ruleForm.info"
                      ></el-input>
                    </el-form-item>

                    <el-form-item label="是否上架" prop="state">
                      <el-switch
                        v-model="ruleForm.state"
                        active-color="#13ce66"
                        inactive-color="#ccc"
                        active-value="1"
                        inactive-value="0"
                      ></el-switch>
                    </el-form-item>

                    <el-form-item>
                      <el-button type="primary" @click="addGoods">立即添加</el-button>
                    </el-form-item>
                  </el-form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import { Toast, Dialog } from "vant";
import qs from "querystring";
export default {
  data() {
    return {
      data: { key: "" },
      fileList: [],
      action: "",
      ruleForm: {
        type_id: "",
        name: "",
        price: "",
        minbuy: "",
        maxbuy: "9999",
        weight: "",
        state: "",
        info: "",
        sales: "",
        img: "",
        sm: []
      },
      options: [
        {
          value: "官方正品",
          label: "官方正品"
        },
        {
          value: "自动发货",
          label: "自动发货"
        },
        {
          value: "不支持无理由退款",
          label: "不支持无理由退款"
        }
      ],

      typeList: []
    };
  },
  mounted() {
    this.action = api.upload;
    this._getType();
    this._getUpkey();
  },
  methods: {
    //上传失败
    imgError(err, file, fileList) {
      this.$message.error(err);
    },
    imgSuccess(response, file, fileList) {
      if (response.status == 200) {
        this.$message({
          message: response.message,
          type: "success"
        });
        this.ruleForm.img = response.data;
      } else {
        this.$message.error(response.message);
        this.fileList = [];
      }
    },

    handleExceed(files, fileList) {
      this.$message.error("上传数量超过限制");
    },
    beforeRemove(file, fileList) {
      return this.$message({
        message: "删除成功",
        type: "success"
      });
    },
    //添加商品
    addGoods() {
      if (!this.ruleForm.name) {
        this.$message({
          message: "商品名称不能为空",
          type: "warning"
        });
      } else if (!this.ruleForm.type_id) {
        this.$message({
          message: "请选择商品所属分类",
          type: "warning"
        });
      } else if (this.ruleForm.price == 0) {
        this.$message({
          message: "商品价格不能为0",
          type: "warning"
        });
      } else {
        this.$axios({
          url: api.goods,
          method: "post",
          data: qs.stringify({
            username: sessionStorage.getItem("adminuser"),
            password: sessionStorage.getItem("adminpass"),
            add: this.ruleForm.name,
            type_id: this.ruleForm.type_id,
            price: this.ruleForm.price,
            minbuy: this.ruleForm.minbuy,
            maxbuy: this.ruleForm.maxbuy,
            weight: this.ruleForm.weight,
            state: this.ruleForm.state,
            sales: this.ruleForm.sales,
            img: this.ruleForm.img,
            info: this.ruleForm.info,
            sm1:this.ruleForm.sm[0],
            sm2:this.ruleForm.sm[1],
            sm3:this.ruleForm.sm[2],
          })
        }).then(res => {
          if (res.data.status == 200) {
            this.$message({
              message: res.data.message,
              type: "success"
            });
            this.ruleForm.name = "";
            this.ruleForm.info = "";
            this.ruleForm.sales = "";
            this.ruleForm.imgs = "";
            this.fileList = [];
            this.ruleForm.sm = []
          } else {
            this.$message.error(res.data.message);
          }
        });
      }
    },

    //获取分类
    _getType() {
      this.$axios({
        url: api.type,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          all: true
        })
      }).then(res => {
        this.typeList = res.data.data;
        if (!this.typeList) {
          Dialog.alert({
            message: "请先创建商品分类哦"
          }).then(() => {
            this.$router.push("/admin/type");
          });
        }
      });
    },
    //获取图片上传key
    _getUpkey() {
      this.$axios({
        url: api.config,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          all: true
        })
      }).then(res => {
        this.data.key = res.data.data.upkey;
      });
    }
  }
};
</script>

<style scoped>
.el-breadcrumb {
  line-height: 40px;
}
</style>


