<template>
  <div>
    <div class="container">
      <div class="row">
        <div class="col-md-2"></div>
        <div class="col-md-8">
          <div class="bf_install">
            <div class="bf_install_title">安装向导</div>
            <div class="bf_step">
              <el-steps :active="active" finish-status="success">
                <el-step></el-step>
                <el-step></el-step>
                <el-step></el-step>
              </el-steps>
            </div>
            <div class="bf_cont" v-if="active==0">
              <p>
                请先去阅读
                <a :href="data.term.url">服务条款</a>
              </p>
              <p>
                <el-button type="primary" size="medium" round plain @click="active=1">我同意条款并继续安装</el-button>
              </p>
            </div>
            <div class="bf_cont" v-if="active==1">
              <p v-html="data.log"></p>
              <p>
                <el-button type="primary" size="medium" round plain @click="active=2">下一步</el-button>
              </p>
            </div>
            <div class="bf_cont" v-if="active==2">
              <el-form :model="ruleForm" label-width="100px" class="demo-ruleForm">
                <el-form-item label="数据库地址">
                  <el-input v-model="ruleForm.host"></el-input>
                </el-form-item>
                <el-form-item label="数据库端口">
                  <el-input v-model="ruleForm.port"></el-input>
                </el-form-item>
                <el-form-item label="数据库用户名">
                  <el-input v-model="ruleForm.user"></el-input>
                </el-form-item>

                <el-form-item label="数据库密码">
                  <el-input v-model="ruleForm.pass"></el-input>
                </el-form-item>

                <el-form-item label="数据库名">
                  <el-input v-model="ruleForm.dbname"></el-input>
                </el-form-item>
              </el-form>
              <p>
                <el-button type="primary" @click="add">立即安装</el-button>
              </p>
            </div>
          </div>
        </div>
        <div class="col-md-2"></div>
      </div>
    </div>
  </div>
</template>
<script>
import api from "@/api";
export default {
  data() {
    return {
      active: 0,
      data: [],
      ruleForm: {
        host: "localhost",
        port: "3306",
        user: "",
        pass: "",
        dbname: ""
      }
    };
  },
  mounted() {
    this.$axios({
      url: api.site
    }).then(res => {
      if (res.data.status != 404) {
        this.$router.push('/')
      }
      
    });

    this._getAuth();
  },
  methods: {
    _getAuth() {
      this.$axios({
        url: api.update
      }).then(res => {
        this.data = res.data.data;
      });
    },
    add() {
      window.location.href =
        api.api +
        "/install/install.php?host=" +
        this.ruleForm.host +
        "&port=" +
        this.ruleForm.port +
        "&user=" +
        this.ruleForm.user +
        "&pass=" +
        this.ruleForm.pass +
        "&dbname=" +
        this.ruleForm.dbname;
    }
  }
};
</script>
<style lang="less" scoped>
@import "../../assets/css/bootstrap.min.css";
.bf_install {
  border: 0.5px solid #ccc;
  border-radius: 10px;
  margin-top: 10px;
  margin-bottom: 20px;
  .bf_install_title {
    height: 40px;
    line-height: 40px;
    text-align: center;
    color: #fff;
    background: #3399ff;
    border-radius: 10px 10px 0 0;
    font-size: 20px;
    border-bottom: 0.5px solid #ccc;
  }
  .bf_step {
    padding: 20px;
  }
  .bf_cont {
    padding: 10px;
    p {
      text-align: center;
      margin: 20px 0;
    }
  }
}
.demo-ruleForm {
  margin-top: 20px;
}
</style>


