<template>
  <div style="background: #f5f5f5;">
    <div class="content-page conn">
      <div class="content">
        <div class="container">
          <div class="row" style="padding:0 10px">
            <div class="navhead">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/admin/index' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item>控制台</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
          </div>
          <div class="row">
            <div class="col-md-6 col-sm-6 col-lg-3">
              <div class="mini-stat clearfix bx-shadow">
                <span class="mini-stat-icon bg-info">
                  <van-icon name="shopping-cart-o" color="#fff" />
                </span>
                <div class="mini-stat-info text-right text-muted">
                  <span class="counter">{{orderover}}</span>
                  平台订单总数
                </div>
              </div>
            </div>

            <div class="col-md-6 col-sm-6 col-lg-3">
              <div class="mini-stat clearfix bx-shadow">
                <span class="mini-stat-icon bg-purple">
                  <van-icon name="browsing-history" color="#fff" />
                </span>
                <div class="mini-stat-info text-right text-muted">
                  <span class="counter">{{todayorder}}</span>
                  今日交易完成
                </div>
              </div>
            </div>

            <div class="col-md-6 col-sm-6 col-lg-3">
              <div class="mini-stat clearfix bx-shadow">
                <span class="mini-stat-icon bg-success">
                  <van-icon name="fire" color="#fff" />
                </span>
                <div class="mini-stat-info text-right text-muted">
                  <span class="counter">{{todaymoney}}</span>
                  今日交易额
                </div>
              </div>
            </div>

            <div class="col-md-6 col-sm-6 col-lg-3">
              <div class="mini-stat clearfix bx-shadow">
                <span class="mini-stat-icon bg-primary">
                  <van-icon name="clock-o" color="#fff" />
                </span>
                <div class="mini-stat-info text-right text-muted">
                  <span class="counter">{{yesterday}}</span>
                  昨日交易额
                </div>
              </div>
            </div>
          </div>

          <div class="row">
            <div class="col-lg-6">
              <div class="portlet">
                <div class="portlet-heading">
                  <h3 class="portlet-title text-dark text-uppercase">快捷导航</h3>
                  <div class="clearfix"></div>
                </div>
                <div id="portlet1" class="panel-collapse collapse in">
                  <div class="portlet-body">
                    <div class="row">
                      <div class="col-md-12" style="height:300px;overflow: hidden;">
                        <van-grid>
                          <van-grid-item
                            icon="setting-o"
                            @click="$router.push('/admin/common')"
                            text="全局设置"
                            style="border-left:.5px solid #edeff2"
                          />
                         
                          <van-grid-item icon="send-gift-o" 
                          @click="$router.push('/admin/swiper')" text="轮播广告" />
                          <van-grid-item icon="thumb-circle-o"  text="Github" @click="Github"/>
                          <van-grid-item
                            icon="fire-o"
                            @click="onUpdate"
                            text="版本更新"
                            :dot="update.version>version"
                          />
                        </van-grid>
                        <ul class="bf_logul">
                         
                          <li v-for="item in late" :key="item.id" class="bf_logli">
                            ☼用户<span style="color:#009966">{{item.user}}</span>
                            于<span style="color:#009966">{{item.endTime | date}}</span>
                            购买了<span style="color:#009966">{{item.gName}}</span>{{item.number}}份
                            
                          </li>
                          
                        </ul>
                        <!-- <div id="container" style="position: relative;height: 320px;"></div> -->
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <!-- /Portlet -->
            </div>
            <!-- end col -->

            <div class="col-lg-6">
              <div class="portlet">
                <!-- /portlet heading -->
                <div class="portlet-heading">
                  <h3 class="portlet-title text-dark text-uppercase">关于我们</h3>
                  
                  <div class="clearfix"></div>
                </div>
                <div id="portlet2" class="panel-collapse collapse in">
                  <div class="portlet-body">
                    <div class="row">
                      <div class="col-md-12" style="height:300px;">
                       
                        <ul class="bf_ul">
                          <li class="bf_li">
                            当前版本：v{{version}}
                            <a v-if="update.version>version" @click="onUpdate">有更新</a>
                          </li>
                          <li class="bf_li">程序名称：{{update.name}}</li>
                          <li class="bf_li">
                            程序作者：捕风阁
                            <a href="https://osuu.net">www.osuu.net</a>
                          </li>
                          <li class="bf_li">作者邮箱：admin@osuu.net</li>
                          <li class="bf_li">
                            问题反馈：
                            <a :href="update.feedback">反馈问题,反馈bug</a>
                          </li>
                          <li class="bf_li">
                            开源交流：
                            <a @click="wxint">{{update.exchange.exname}}</a>
                          </li>
                          <li class="bf_li">
                            服务条款：
                            <a :href="update.term.url">{{update.term.exname}}</a>
                          </li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              <!-- /Portlet -->
            </div>
            <!-- end col -->
          </div>
        </div>
      </div>
     
    </div>
     <van-dialog v-model="wximg" :title="update.exchange.exname" show-cancel-button>
        <img :src="update.exchange.imgurl" width="100%"/>
      </van-dialog>
  </div>
</template>

<script>
import api from "@/api";
import qs from "querystring";
import { Dialog, Collapse, CollapseItem } from "vant";

export default {
  data() {
    return {
      todayorder: "0",
      orderover: "0",
      todaymoney: "0",
      yesterday: "0",
      version: "",
      update: [],
      wximg: false,
      late:[],

    
    };
  },
  filters: {
     //时间戳转日期
    date: function(time) {
      var date = new Date(time * 1000); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate() + " ";
      var h = date.getHours() + ":";
      var m = date.getMinutes() + ":";
      var s = date.getSeconds();
      return Y + M + D + h + m + s;
    },
  },
  mounted() {
    this._getOrder();
  },
  methods: {
    //微信群
    wxint() {
      this.wximg = true;
    },
    //Github
    Github(){
      window.open("https://github.com/osuuu/BF_SHOP");    
    },
    //获取nav信息
    _getOrder() {
      this.$axios({
        url: api.config,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          home: true
        })
      }).then(res => {
        this.late = res.data.late.reverse()
        this.version = res.data.version;
        this.todayorder = res.data.todayorder;
        this.orderover = res.data.orderover;
        let money = 0;
        let yester = 0;
        //今日交易额
        res.data.todaymoney.map(function(item, index) {
          money += Number(item.money);
        });
        this.todaymoney = money;
        //昨日交易额
        res.data.yesterday.map(function(item, index) {
          yester += Number(item.money);
        });
        this.yesterday = yester;
        this._getUpdate();
      });
    },
    //更新按钮
    onUpdate() {
      if (this.version < this.update.version) {
        window.location.href = this.update.update_link;
      } else {
        Dialog.alert({
          message: "当前已是最新版本"
        }).then(() => {});
      }
    },
    //获取更新
    _getUpdate() {
      this.$axios({
        url: api.update
      }).then(res => {
        this.update = res.data.data;
        if (this.version < res.data.data.version) {
          this.$notify({
            title: "新版本提示",
            dangerouslyUseHTMLString: true,
            message: res.data.data.description
          });
        }
        if (res.data.data.announce) {
          this.$notify({
            title: "公告",
            message: res.data.data.announce,
            type: "warning",
            offset: 100,
            duration: 8000
          });
        }
      });
    }
  }
};
</script>

<style scoped>
.el-breadcrumb {
  line-height: 40px;
}
.van-icon {
  line-height: 60px;
  font-size: 30px;
}
.bf_ul {
  padding: 0 10px;
}
.bf_li {
  height: 40px;
  line-height: 40px;
  font-size: 14px;
  border-bottom: 0.4px solid #edeff2;
}
a {
  cursor: pointer;
}
.bf_logul{
  border: 0.4px solid #edeff2;
  border-top: 0;
  padding: 5px 10px 0 10px;
      height: 215px;
    overflow: hidden;
}
.bf_logli{
  height: 30px;
  line-height: 30px;
  font-size: 14px;
 white-space: nowrap;  /* 一行 */
overflow: hidden;  /* 溢出隐藏 */
text-overflow: ellipsis;  /* 超出内容显示省略号 */
}
.bf_no{
  text-align: center;
}
</style>


