<template>
  <div>
    <div :is="template"></div>
  </div>
</template>

<script>
import api from "@/api";
import tmp1_mobile from "./template/bf_shop/mobile_index";
import tmp1_pc from "./template/bf_shop/pc_index";
export default {
  data() {
    return {
      template: ""
    };
  },
  components: {
    tmp1_mobile,
    tmp1_pc
  },
  mounted() {
    if (document.body.clientWidth < 990) {
      this.template = tmp1_mobile;
    } else {
      this.template = tmp1_pc;
    }
    //目前一个模板，无需从后台调用
    // this.$axios({
    //   url: api.site
    // }).then(res => {
    //   this.template = res.data.data.template;
    // });
    
  }
};
</script>

<style scoped>
</style>


