<template>
  <div>
    <div class="widescreen fixed-left">
      <div id="wrapper">
        <div class="topbar">
          <div class="navbar navbar-default" role="navigation">
            <div class="container">
              <div class>
                <div class="pull-left">
                  <button @click="navs" class="button-menu-mobile open-left logobtn">
                    <i class="fa fa-bars"></i>
                  </button>
                  <div class="text-center logotext">
                    <a href="/" class="logo">BF控制台</a>
                  </div>
                  <span class="clearfix"></span>
                </div>

                <ul class="nav navbar-nav navbar-right pull-right">
                  <li class="dropdown">
                    <a
                      href
                      class="dropdown-toggle profile"
                      data-toggle="dropdown"
                      aria-expanded="true"
                    >
                      <img
                        src="@/assets/images/tx.png"
                        alt="user-img"
                        style="vertical-align: middle;"
                        class="img-circle"
                      />
                    </a>

                    <ul class="dropdown-menu">
                      <li>
                        <a data-toggle="modal" data-target="#myEditmm">
                          <i class="fa fa-key"></i>&nbsp; 重置密码
                        </a>
                      </li>

                      <li>
                        <a @click="logout">
                          <i class="fa fa-external-link"></i>&nbsp; 退出登陆
                        </a>
                      </li>
                    </ul>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>

        <div
          class="modal fade"
          id="myEditmm"
          tabindex="-1"
          role="dialog"
          aria-labelledby="myModalLabel"
        >
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
                <h4 class="modal-title" id="myModalLabel">修改密码</h4>
              </div>
              <div class="modal-body">
                <div class="form-group">
                  <label for="old">当前密码:</label>
                  <el-input v-model="old" id="old"></el-input>
                </div>
                <div class="form-group">
                  <label for="newa">新密码:</label>
                  <el-input v-model="newa" id="newa"></el-input>
                </div>
                <div class="form-group">
                  <label for="newb">确认新密码:</label>
                  <el-input v-model="newb" id="newb"></el-input>
                </div>
              </div>
              <div class="modal-footer text-center">
                <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
                <button type="button" class="btn btn-primary" @click="setadmin">确认</button>
              </div>
            </div>
          </div>
        </div>

        <div class="left side-menu" :class="[nav?'nava':'navb']">
          <div class="sidebar-inner slimscrollleft">
            <div id="sidebar-menu">
              <ul>
                <li>
                  <a href="/admin/index" class="waves-effect">
                    <i class="glyphicon glyphicon glyphicon-home"></i>
                    <span>首页</span>
                  </a>
                </li>
                <li>
                  <a href="/admin/order" class="waves-effect">
                    <i class="glyphicon glyphicon-leaf"></i>
                    <span>订单管理</span>
                  </a>
                </li>

                <li class="has_sub">
                  <a @click="menus(1)" class="waves-effect" :class="[menu==1?'subdrop':'']">
                    <i class="glyphicon glyphicon-list-alt"></i>
                    <span>商品管理</span>
                    <span class="pull-right">
                      <van-icon :name="menu==1?'arrow-down':'arrow'" style="margin-top:3px" />
                    </span>
                  </a>
                  <ul class="list-unstyled" v-if="menu==1">
                    <li>
                      <a href="/admin/addgoods">添加商品</a>
                    </li>
                    <li>
                      <a href="/admin/goods">商品管理</a>
                    </li>
                    <li>
                      <a href="/admin/promotion">促销管理</a>
                    </li>
                    <li>
                      <a href="/admin/type">分类管理</a>
                    </li>
                  </ul>
                </li>

                <li class="has_sub">
                  <a @click="menus(2)" class="waves-effect" :class="[menu==2?'subdrop':'']">
                    <i class="glyphicon glyphicon-briefcase"></i>
                    <span>卡密管理</span>
                    <span class="pull-right">
                      <van-icon :name="menu==2?'arrow-down':'arrow'" style="margin-top:3px" />
                    </span>
                  </a>
                  <ul class="list-unstyled" v-if="menu==2">
                    <li>
                      <a href="/admin/addkm">添加卡密</a>
                    </li>
                    <li>
                      <a href="/admin/kmlist">卡密列表</a>
                    </li>
                  </ul>
                </li>
                <li class="has_sub">
                  <a @click="menus(3)" class="waves-effect" :class="[menu==3?'subdrop':'']">
                    <i class="glyphicon glyphicon-dashboard"></i>
                    <span>系统设置</span>
                    <span class="pull-right">
                      <van-icon :name="menu==3?'arrow-down':'arrow'" style="margin-top:3px" />
                    </span>
                  </a>
                  <ul class="list-unstyled" v-show="menu===3">
                    <li>
                      <a href="/admin/common">站点设置</a>
                    </li>
                    <li>
                      <a href="/admin/swiper">轮播广告</a>
                    </li>
                    <li>
                      <a href="/admin/pay">支付设置</a>
                    </li>
                    <li>
                      <a href="/admin/email">邮箱设置</a>
                    </li>
                     <li>
                      <a href="/admin/logo">首页LOGO</a>
                    </li>
                    <li>
                      <a href="/admin/template">模板设置</a>
                    </li>
                  </ul>
                </li>
              </ul>
              <div class="clearfix"></div>
            </div>
            <div class="clearfix"></div>
          </div>
        </div>

        <router-view />

        <footer class="footer text-right">
          &copy; 版权所有
          <a href="https://www.osuu.net" target="_blank">捕风阁</a>
          
        </footer>
      </div>
    </div>
  </div>
</template>

<script>
import "@/assets/js/jquery.min.js";
import "@/assets/js/bootstrap.min.js";
import api from "@/api";
import qs from "querystring";
import { Dialog, Toast } from "vant";

export default {
  data() {
    return {
      nav: false,
      menu: 0,
      old: "",
      newa: "",
      newb: "",

      data: [],
      WebTitle: ""
    };
  },
  mounted() {
    this.author();
    this._getAuth()
  },
  methods: {
    setadmin() {
      if (!this.old) {
        this.$message.error("旧密码不能为空");
      } else if (!this.newa && !this.newb) {
        this.$message.error("新密码不能为空");
      } else if (this.newa != this.newb) {
        this.$message.error("两次密码输入不一致");
      } else if (this.old != sessionStorage.getItem("adminpass")) {
        this.$message.error("旧密码不正确");
      } else {
        this.$axios({
          url: api.config,
          method: "post",
          data: qs.stringify({
            username: sessionStorage.getItem("adminuser"),
            password: sessionStorage.getItem("adminpass"),
            reset: this.newa
          })
        }).then(res => {
          if (res.data.status == 200) {
            this.$message({
              message: res.data.message,
              type: "success"
            });
            sessionStorage.setItem("adminpass", this.newa);
            this.old = "";
            this.newa = "";
            this.newb = "";
            $("#myEditmm").modal("hide");
          } else {
            this.$message.error(res.data.message);
          }
        });
      }
    },
    logout() {
      Dialog.confirm({
        message: "确定退出登录吗"
      })
        .then(() => {
          this.$router.push("/bf-login");
          sessionStorage.removeItem("adminuser", true);
          sessionStorage.removeItem("adminpass", true);
          sessionStorage.removeItem("token", true);
        })
        .catch(() => {});
    },
    navs() {
      if (this.nav) {
        this.nav = false;
      } else {
        this.nav = true;
      }
    },
    menus(num) {
      if (this.menu != num) {
        this.menu = num;
      } else {
        this.menu = 0;
      }
    },
    _getAuth() {
      this.$axios({
        url: api.auth
      }).then(res => {
        if (res.data.status == 100) {
          this.show = false;
          Dialog.alert({
            message: "当前站点未授权"
          }).then(() => {
            window.location.href = res.data.url;
          });
        }
      });
    },
    author() {
      this.$axios({
        url: api.login,
        method: "post",
        params: {
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass")
        }
      })
        .then(res => {
          if (res.data.status != 200) {
            Dialog.alert({
              message: "登录失效 请重新登录"
            }).then(() => {
              this.$router.push("/bf-login");
            });
          }
        })
        .catch(err => {
          Dialog.alert({
            message: "登录失效 请重新登录"
          }).then(() => {
            this.$router.push("/bf-login");
          });
        });
    }
  }
};
</script>
<style>
@import "../../assets/css/bootstrap.min.css";
@import "../../assets/css/combined.css";
@import "../../assets/css/animate.css";
@import "../../assets/css/style.css";
@import "../../assets/font-awesome/css/font-awesome.min.css";

.navhead {
  background: #fff;
  padding: 0 10px;
  height: 40px;
  margin-bottom: 10px;

  border-radius: 2px;
}
.el-breadcrumb {
  line-height: 40px;
}
.conn {
  background: #f5f5f5;
}
</style>


