<template>
  <div class="bf_mobile">
    <div class="bf_head">
      <h1>{{data.name}}</h1>
      <el-button
        type="primary"
        plain
        round
        class="tp1_cx"
        size="medium"
        @click="$router.push('/query')"
      >订单查询</el-button>
    </div>
    <div class="swipe_bg"></div>
    <div class="block">
      <el-carousel height="150px">
        <el-carousel-item v-for="item in imgs" :key="item.id">
          <a :href="item.url" target="_blank">
            <img :src="item.img" class="swipe_img" alt />
          </a>
        </el-carousel-item>
      </el-carousel>
    </div>
    <div class="bf_goods">
      <div class="bf_title">
        <i class="el-icon-takeaway-box" style="font-size:20px"></i>
        <h2>商品推荐</h2>
        <el-button type="success" plain round size="mini" class="bf_kf" @click="kf">联系客服</el-button>
      </div>
      <div class="bf_goodlist">
        <div
          class="bf_goods_nav"
          v-for="(item,index) in goodsList"
          :key="index"
          @click="$router.push('/goods/'+item.id+'.html')"
        >
          <img :src="`/upload/${item.img}`" alt class="bf_goodsImg" />
          <h3 class="bf_h3">
            <div class="bf_tj">荐</div>
            {{item.name}}
          </h3>
          <h4 class="bf_h4">
            <span class="bf_price">
              <em style="font-size:12px">￥</em>
              {{item.price}}
            </span>
            <button class="bf_gobuy">去看看</button>
          </h4>
        </div>
      </div>
    </div>

    <div class="bf_all_goods">
      <div class="bf_title">
        <i class="el-icon-takeaway-box" style="font-size:20px"></i>
        <h2>商品分类</h2>
        <el-select v-model="typeId" placeholder="请选择分类" size="small" style="float:right">
          <el-option v-for="item in typeList" :key="item.id" :label="item.name" :value="item.id"></el-option>
        </el-select>
      </div>
      <div class="bf_goodlist">
        <div
          class="bf_goods_nav"
          v-for="(item,index) in moreList"
          :key="index"
          @click="$router.push('/goods/'+item.id+'.html')"
        >
          <img :src="`/upload/${item.img}`" alt class="bf_goodsImg" />
          <h3 class="bf_h3">{{item.name}}</h3>
          <h4 class="bf_h4">
            <span class="bf_price">
              <em style="font-size:12px">￥</em>
              {{item.price}}
            </span>
            <button class="bf_gobuy">去看看</button>
          </h4>
        </div>
      </div>
    </div>

    <div class="bf_footer">
      <div v-html="data.link" class="bf_link"></div>
      <div v-html="data.copy" class="bf_copy"></div>
    </div>
  </div>
</template>

<script>
import { Toast, Dialog } from "vant";
import api from "@/api";
import qs from "querystring";
export default {
  data() {
    return {
      imgs: [],
      goodsList: [],
      moreList: [],
      typeList: [],
      data: [],
      typeId: ""
    };
  },
  mounted() {
    this._getImgs();
    Toast.loading({
      message: "商品加载中...",
      forbidClick: true
    });
    this._getSet();

    this._getType();
  },
  methods: {
    //联系客服
    kf() {
      Dialog.confirm({
        // title: '标题',
        message: "客服QQ: " + this.data.kf + " 点击确定联系"
      })
        .then(() => {
          window.location.href =
            "http://wpa.qq.com/msgrd?v=3&uin=" +
            this.data.kf +
            "&site=qq&menu=yes";
        })
        .catch(() => {
          // on cancel
        });
    },
    //获取轮播图
    _getImgs() {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          swiper: true
        })
      })
        .then(res => {
          this.imgs = res.data.data;
        })
        .catch(err => {});
    },
    //获取全部商品
    _getGoods() {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          goods: true
        })
      }).then(res => {
        let compare = function(obj1, obj2) {
          if (obj1.weight > obj2.weight) {
            return -1;
          } else if (obj1.weight < obj2.weight) {
            return 1;
          } else {
            return 0;
          }
        };

        let arr = res.data.data.sort(compare);
        let Num = 0;
        if (res.data.data.length < this.data.rec) {
          Num = res.data.data.length;
        } else {
          Num = this.data.rec;
        }
        for (let i = 0; i < Num; i++) {
          //推荐商品
          this.goodsList.push(arr[i]);
        }
        //其他商品
        this.moreList = arr.splice(this.data.rec);
      });
    },
    //获取商品分类
    _getType() {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          type: true
        })
      }).then(res => {
        let compare = function(obj1, obj2) {
          if (obj1.weight > obj2.weight) {
            return -1;
          } else if (obj1.weight < obj2.weight) {
            return 1;
          } else {
            return 0;
          }
        };
        this.typeList = res.data.data.sort(compare);
      });
    },
    //获取指定分类商品
    _getIdgoods(id) {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          id
        })
      }).then(res => {
        this.moreList = res.data.data;
      });
    },
    //获取部分设置
    _getSet() {
      this.$axios({
        url: api.site
      })
        .then(res => {
          this.data = res.data.data;
          //获取商品
          this._getGoods();
          //是否弹出公告
          if (this.data.gg) {
            Dialog.alert({
              title: "公告",
              message: this.data.gg
            }).then(() => {
              // on close
            });
          }
        })
        .catch(err => {});
    }
  },
  watch: {
    typeId(newVal) {
      Toast.loading({
        message: "加载中...",
        forbidClick: true
      });
      this._getIdgoods(newVal);
    }
  }
};
</script>

<style lang="less" scoped>
@import "../../../assets/css/reset.css";
.bf_mobile {
  width: 100%;
  overflow: hidden;
  position: relative;
  background: #fff;
  .bf_head {
    height: 50px;
    background: #fff;
    border-bottom: 1px solid #ccc;
    box-shadow: 1px 1px 5px #888888;
    padding: 0 10px;
    h1 {
      display: inline-block;
      font-size: 20px;
      line-height: 50px;
    }
    .tp1_cx {
      float: right;
      margin-top: 7px;
    }
  }
  .swipe_bg {
    background-image: linear-gradient(0deg, #3399ff, #6633ff 50%);
    position: absolute;
    top: 50px;
    left: -25%;
    height: 140px;
    width: 150%;
    border-bottom-left-radius: 100%;
    border-bottom-right-radius: 100%;
  }
  .block {
    padding: 0 10px;
    margin-top: 15px;
    .swipe_img {
      width: 100%;
      height: 150px;
      border-radius: 10px;
    }
  }
  .bf_goods,
  .bf_all_goods {
    padding: 0 10px;
    margin-top: 15px;
    .bf_title {
      height: 30px;
      h2 {
        display: inline-block;
        line-height: 30px;
        font-size: 20px;
      }
      .bf_kf {
        float: right;
      }
    }
    .bf_goodlist {
      margin: 10px 0;
      padding: 0 10px;
      display: flex; /* 设置为弹性容器 */
      justify-content: space-between;
      flex-wrap: wrap;
      .bf_goods_nav {
        height: 215px;
        //   padding: 1px;
        margin-bottom: 20px;
        width: 48%;
        border-radius: 5px;
        box-shadow: 1px 1px 5px #888888;
        .bf_goodsImg {
          width: 100%;
          height: 150px;
          border-radius: 5px 5px 0 0;
        }
        .bf_h4 {
          height: 25px;
          padding: 0 6px;
          margin-top: 4px;
          .bf_price {
            color: red;
            font-size: 16px;
          }
          .bf_gobuy {
            border: 1px solid #ccc;
            border-radius: 10px;
            background: #fff;
            height: 18px;
            font-size: 14px;
            line-height: 18px;
            float: right;
          }
        }
        .bf_h3 {
          padding: 0 6px;
          margin-top: 6px;
          font-size: 14px;
          height: 26px;
          white-space: nowrap; /* 一行 */
          overflow: hidden; /* 溢出隐藏 */
          text-overflow: ellipsis; /* 超出内容显示省略号 */
          .bf_tj {
            display: inline-block;
            margin-right: 4px;
            background: red;
            color: #fff;
            width: 20px;
            height: 16px;
            line-height: 16px;
            border-radius: 6px;
            font-size: 12px;
            text-align: center;
          }
        }
      }
    }
  }
  .bf_footer {
    // height: 100px;
    padding: 10px;
    background: #fff;
    // box-shadow: 1px 1px 5px #888888;
    .bf_link {
      text-align: center;
      font-size: 14px;
      margin-top: 10px;
    }
    .bf_copy {
      text-align: center;
      font-size: 14px;
      margin-top: 10px;
      margin-bottom: 10px;
    }
  }
}
</style>


