<template>
  <div>
    <div class="content-page conn">
      <div class="content">
        <div class="container">
          <div class="row" style="padding:0 10px">
            <div class="navhead">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/admin/index' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item>邮件设置</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12">
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h3 class="panel-title">
                    <font>邮件设置</font>
                  </h3>
                </div>
                <div class="panel-body">
                  <el-form :model="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-alert :title="warning" type="warning" effect="dark"></el-alert>
                    <el-form-item label="SMTP服务器">
                      <el-input v-model="ruleForm.mail_stmp" placeholder="smtp.exmail.qq.com"></el-input>
                    </el-form-item>
                    <el-form-item label="SMTP端口">
                      <el-input v-model="ruleForm.mail_port" placeholder="465"></el-input>
                    </el-form-item>
                    <el-form-item label="邮箱账号">
                      <el-input v-model="ruleForm.mail_usr" placeholder="demo@mydemo.com"></el-input>
                    </el-form-item>
                    <el-form-item label="邮箱密码">
                      <el-input v-model="ruleForm.mail_pwd" placeholder="请输入密码"></el-input>
                    </el-form-item>
                    <hr />
                    <el-form-item label="收件邮箱">
                      <el-input v-model="ruleForm.mail_rece" placeholder="请输入您的收件邮箱"></el-input>
                    </el-form-item>

                    <el-form-item>
                      <el-button type="warning" size="medium" @click="onsend">发送测试</el-button>
                      <el-button type="primary" size="medium" style="margin-left:0" @click="addEmail">立即修改</el-button>
                    </el-form-item>
                  </el-form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import { Toast, Dialog } from "vant";
import qs from "querystring";
export default {
  data() {
    return {
      key: "",
      warning:
        "如果为QQ邮箱需先开通SMTP，且密码要填写QQ邮箱独立授权码，QQ邮箱SMTP服务器为 smtp.qq.com 腾讯企业邮箱SMTP服务器为smtp.exmail.qq.com 设置完成请发件测试一下，以免业务报错",
      ruleForm: {
        mail_stmp: "",
        mail_port: "",
        mail_usr: "",
        mail_pwd: "",
        mail_rece: ""
      }
    };
  },
  mounted() {
    this._getEmail();
    this._getkey();
  },
  methods: {
    addEmail() {
      this.$axios({
        url: api.email,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          edit: true,
          mail_stmp: this.ruleForm.mail_stmp,
          mail_port: this.ruleForm.mail_port,
          mail_usr: this.ruleForm.mail_usr,
          mail_pwd: this.ruleForm.mail_pwd,
          mail_rece: this.ruleForm.mail_rece
        })
      }).then(res => {
        if (res.data.status == 200) {
          this.$message({
            message: res.data.message,
            type: "success"
          });
        } else {
          this.$message.error(res.data.message);
        }
      });
    },
    //发送测试
    onsend() {
      Toast.loading({
        message: "加载中...",
        forbidClick: true
      });
      this.$axios({
        url: api.send,
        params: {
          key: this.key,
          rece: this.ruleForm.mail_rece
        }
      }).then(res => {
        if (res.data.status == 200) {
          this.$message({
            message: res.data.message,
            type: "success"
          });
        } else {
          this.$message.error(res.data.message);
        }
      });
    },
    //获取邮箱内容
    _getEmail() {
      this.$axios({
        url: api.email,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          all: true
        })
      }).then(res => {
        this.ruleForm = res.data.data;
      });
    },
    //获取邮箱key
    _getkey() {
      this.$axios({
        url: api.config,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          all: true
        })
      }).then(res => {
        this.key = res.data.data.monit;
      });
    }
  }
};
</script>

<style scoped>
.el-alert {
  margin-bottom: 30px;
}
.el-breadcrumb {
  line-height: 40px;
}
</style>


