<template>
  <div class="bg">
    <div class="sucaihuo-container">
      <div class="form-bg">
        <div class="container">
          <div class="row">
            <div class="col-md-offset-3 col-md-6 container">
              <div class="form-horizontal">
                <span class="heading">管理员登录</span>
                <div class="form-group">
                  <input type="text" class="form-control" v-model="username" placeholder="用户名" />
                  <van-icon name="manager-o" color="#999" />
                </div>
                <div class="form-group help">
                  <input type="password" class="form-control" v-model="password" placeholder="密码" />
                  <van-icon name="eye-o" color="#999" />
                </div>
                <div class="form-group" style="margin-bottom:30px">
                  <button
                    type="button"
                    @click="login"
                    class="btn btn-default btn-block"
                    style="outline:none;"
                  >登录</button>
                </div>

                <footer class="copyright">
                  Copyright © 2020
                  <a href="http://faka.h234.cn">BF发卡网</a> All Rights Reserved.
                </footer>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import { Toast } from "vant";
export default {
  data() {
    return {
      username: "",
      password: ""
    };
  },
  methods: {
    // 登录方法
    login() {
      if (this.username == "") {
        this.$message({
          message: "账号不能为空",
          type: "warning"
        });
      } else if (this.password == "") {
        this.$message({
          message: "密码不能为空",
          type: "warning"
        });
      } else {
        Toast.loading({
          message: "登录中...",
          forbidClick: true,
          loadingType: "spinner"
        });
        this.$axios({
          url: api.login,
          params: {
            username: this.username,
            password: this.password
          }
        })
          .then(res => {
            if (res.data.status == -1) {
              Toast.fail(res.data.message);
            } else if (res.data.status == 100) {
              Toast.fail(res.data.message);
            } else if (res.data.status == 200) {
              Toast.success(res.data.message);
              sessionStorage.setItem("adminuser", this.username);
              sessionStorage.setItem("adminpass", this.password);
              sessionStorage.setItem("token", true);
              this.$router.push("/admin/index");
              Toast.loading({
                message: "加载中...",
                forbidClick: true
              });
            }
          })
          .catch(err => {
            Toast.fail("系统繁忙");
          });
      }
    }
  }
};
</script>

<style scoped>
@import "../../../node_modules/bootstrap/dist/css/bootstrap.min.css";
@import "../../assets/css/bootstrap.min.css";
.bg {
  background: linear-gradient(#6666ff, #00cc99);
  color: #5b5b5d;
  font-family: "Microsoft YaHei", "Segoe UI", "Lucida Grande", Helvetica, Arial,
    sans-serif;
}
* {
  margin: 0;
  margin: 0;
}

.container {
  margin: 0 auto;
}
.sucaihuo-container {
  text-align: center;
  overflow: hidden;
}
.sucaihuo-header {
  padding: 3em 190px 2em;
  letter-spacing: -1px;
  text-align: center;
}
.sucaihuo-header h1 {
  color: #404d5b;
  font-weight: 600;
  font-size: 2em;
  line-height: 1;
  margin-bottom: 0;
  font-family: "Segoe UI", "Lucida Grande", Helvetica, Arial, "Microsoft YaHei",
    FreeSans, Arimo, "Droid Sans", "wenquanyi micro hei", "Hiragino Sans GB",
    "Hiragino Sans GB W3", "FontAwesome", sans-serif;
}
.sucaihuo-content {
  min-height: 60vh;
  height: 60vh;
  font-size: 150%;
  padding: 1em 0;
}
.bgcolor-3 {
  background: #e8e8e8;
}
*,
*:after,
*:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.form-horizontal {
  background: #fff;
  padding-bottom: 40px;
  border-radius: 15px;
  text-align: center;
}
.form-horizontal .heading {
  display: block;
  font-size: 25px;
  font-weight: 700;
  padding: 35px 0;
  border-bottom: 1px solid #f0f0f0;
  margin-bottom: 30px;
}
.form-horizontal .form-group {
  padding: 0 40px;
  margin: 0 0 25px 0;
  position: relative;
}
.form-horizontal .form-control {
  background: #f0f0f0;
  border: none;
  border-radius: 20px;
  box-shadow: none;
  padding: 0 20px 0 45px;
  height: 40px;
  transition: all 0.3s ease 0s;
}
.form-horizontal .form-control:focus {
  background: #e0e0e0;
  box-shadow: none;
  outline: 0 none;
}
.form-horizontal .form-group i {
  position: absolute;
  top: 12px;
  left: 60px;
  font-size: 17px;
  color: #c8c8c8;
  transition: all 0.5s ease 0s;
}
.form-horizontal .form-control:focus + i {
  color: #00b4ef;
}
.form-horizontal .fa-question-circle {
  display: inline-block;
  position: absolute;
  top: 12px;
  right: 60px;
  font-size: 20px;
  color: #808080;
  transition: all 0.5s ease 0s;
}
.form-horizontal .fa-question-circle:hover {
  color: #000;
}
.form-horizontal .main-checkbox {
  float: left;
  width: 20px;
  height: 20px;
  background: #11a3fc;
  border-radius: 50%;
  position: relative;
  margin: 5px 0 0 5px;
  border: 1px solid #11a3fc;
}
.form-horizontal .main-checkbox label {
  width: 20px;
  height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  cursor: pointer;
}
.form-horizontal .main-checkbox label:after {
  content: "";
  width: 10px;
  height: 5px;
  position: absolute;
  top: 5px;
  left: 4px;
  border: 3px solid #fff;
  border-top: none;
  border-right: none;
  background: transparent;
  opacity: 0;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
}
.form-horizontal .main-checkbox input[type="checkbox"] {
  visibility: hidden;
}
.form-horizontal .main-checkbox input[type="checkbox"]:checked + label:after {
  opacity: 1;
}
.form-horizontal .text {
  float: left;
  margin-left: 7px;
  line-height: 20px;
  padding-top: 5px;
  text-transform: capitalize;
}
.form-horizontal .btn {
  float: right;
  font-size: 14px;
  color: #fff;
  background: #00b4ef;
  border-radius: 30px;
  padding: 10px 25px;
  border: none;
  text-transform: capitalize;
  transition: all 0.5s ease 0s;
}
@media only screen and (max-width: 479px) {
  .form-horizontal .form-group {
    padding: 0 25px;
  }
  .form-horizontal .form-group i {
    left: 45px;
  }
  .form-horizontal .btn {
    padding: 10px 20px;
  }
}

.form-bg {
  padding: 180px 0;
}

.form-horizontal {
  background: #fff;
  padding-bottom: 40px;
  border-radius: 15px;
  text-align: center;
}
.form-horizontal .heading {
  display: block;
  font-size: 25px;
  font-weight: 700;
  padding: 35px 0;
  border-bottom: 1px solid #f0f0f0;
