<template>
  <div>
    <div class="content-page conn">
      <div class="content">
        <div class="container">
          <div class="row" style="padding:0 10px">
            <div class="navhead">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/admin/index' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item>支付设置</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12">
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h3 class="panel-title">
                    <font>支付设置</font>
                  </h3>
                </div>
                <div class="panel-body">
                  <div class="explain">
                    <div class="title">{{update.explain.exname}}</div>
                    <div v-html="update.explain.content"></div>
                  </div>

                  <el-form :model="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="支付宝支付">
                      <el-select v-model="ruleForm.alipay" placeholder="请选择">
                        <el-option label="关闭" value="0"></el-option>
                        <el-option label="官方接口" value="1"></el-option>
                        <el-option label="易支付接口" value="2"></el-option>
                        <el-option label="码支付接口" value="3"></el-option>
                        <el-option label="当面付接口" value="4"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="微信支付">
                      <el-select v-model="ruleForm.wxpay" placeholder="请选择">
                        <el-option label="关闭" value="0"></el-option>
                        <el-option label="官方接口" value="1" disabled></el-option>
                        <el-option label="易支付接口" value="2"></el-option>
                        <el-option label="码支付接口" value="3"></el-option>
                      </el-select>
                    </el-form-item>
                    <el-form-item label="QQ支付">
                      <el-select v-model="ruleForm.qqpay" placeholder="请选择">
                        <el-option label="关闭" value="0"></el-option>
                        <el-option label="官方接口" value="1" disabled></el-option>
                        <el-option label="易支付接口" value="2"></el-option>
                        <el-option label="码支付接口" value="3"></el-option>
                      </el-select>
                    </el-form-item>

                    <!-- 支付宝官方接口 -->
                    <div v-show="ruleForm.alipay==1">
                      <hr />
                      <el-form-item label="合作身份者ID">
                        <el-input v-model="ruleForm.ali_partner" placeholder="请输入支付宝合作身份者ID"></el-input>
                      </el-form-item>
                      <el-form-item label="收款支付宝">
                        <el-input v-model="ruleForm.ali_seller" placeholder="请输入收款支付宝账号"></el-input>
                      </el-form-item>
                      <el-form-item label="安全校验码">
                        <el-input v-model="ruleForm.ali_key" placeholder="请输入支付宝安全校验码"></el-input>
                      </el-form-item>
                    </div>
                    <!-- 支付宝当面付接口 -->
                    <div v-show="ruleForm.alipay==4">
                      <hr />
                      <el-form-item label="APPID">
                        <el-input v-model="ruleForm.f2f_appid" placeholder="请输入支付宝当面付APPID"></el-input>
                      </el-form-item>
                      <el-form-item label="支付宝公钥">
                        <el-input v-model="ruleForm.f2f_public" placeholder="请输入支付宝公钥"></el-input>
                      </el-form-item>
                      <el-form-item label="商户私钥">
                        <el-input v-model="ruleForm.f2f_private" placeholder="请输入支付宝商户私钥"></el-input>
                      </el-form-item>
                    </div>
                    <!-- 微信官方接口 -->
                    <div v-show="ruleForm.wxpay==1">
                      <hr />
                      <el-form-item label="微信APPID">
                        <el-input v-model="ruleForm.wx_appid" placeholder="请输入微信支付APPID"></el-input>
                      </el-form-item>
                      <el-form-item label="微信MCHID">
                        <el-input v-model="ruleForm.wx_mchid" placeholder="请输入微信支付MCHID"></el-input>
                      </el-form-item>
                      <el-form-item label="微信KEY">
                        <el-input v-model="ruleForm.wx_key" placeholder="请输入微信支付KEY"></el-input>
                      </el-form-item>
                      <el-form-item label="微信APPSECRET">
                        <el-input v-model="ruleForm.wx_secret" placeholder="请输入微信支付APPSECRET"></el-input>
                      </el-form-item>
                    </div>
                    <!-- QQ官方接口 -->
                    <div v-show="ruleForm.qqpay==1">
                      <hr />
                      <el-form-item label="QQ-MCHID">
                        <el-input v-model="ruleForm.qq_mchid" placeholder="请输入QQ钱包MCHID"></el-input>
                      </el-form-item>
                      <el-form-item label="QQ-MCHKEY">
                        <el-input v-model="ruleForm.qq_mchkey" placeholder="请输入QQ钱包MCHKEY"></el-input>
                      </el-form-item>
                    </div>

                    <!-- 易支付接口 -->
                    <div v-show="ruleForm.alipay==2 || ruleForm.wxpay==2 || ruleForm.qqpay==2">
                      <hr />
                      <el-form-item label="接口地址">
                        <el-input v-model="ruleForm.epayurl" placeholder="请输入易支付接口网址"></el-input>
                      </el-form-item>
                      <el-form-item label="商户ID">
                        <el-input v-model="ruleForm.epayid" placeholder="请输入易支付商户ID"></el-input>
                      </el-form-item>
                      <el-form-item label="商户密匙">
                        <el-input v-model="ruleForm.epaykey" placeholder="请输入易支付商户密匙"></el-input>
                      </el-form-item>
                    </div>
                    <!-- 码支付接口 -->
                    <div v-show="ruleForm.alipay==3 || ruleForm.wxpay==3 || ruleForm.qqpay==3">
                      <hr />
                      <el-form-item label="商户ID">
                        <el-input v-model="ruleForm.codeid" placeholder="请输入码支付商户ID"></el-input>
                      </el-form-item>
                      <el-form-item label="商户密匙">
                        <el-input v-model="ruleForm.codekey" placeholder="请输入码支付商户密匙"></el-input>
                      </el-form-item>
                    </div>
                    <el-form-item>
                      <el-button type="primary" @click="addPay">立即添加</el-button>
                    </el-form-item>
                  </el-form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import { Toast, Dialog } from "vant";
import qs from "querystring";
export default {
  data() {
    return {
      update: [],
      ruleForm: {
        alipay: "",
        wxpay: "",
        qqpay: "",

        epayurl: "",
        epayid: "",
        epaykey: "",

        codeid: "",
        codekey: "",

        ali_partner: "",
        ali_seller: "",
        ali_key: "",

        f2f_appid: "",
        f2f_public: "",
        f2f_private: "",

        wx_appid: "",
        wx_mchid: "",
        wx_key: "",
        wx_secret: "",
        qq_mchid: "",
        qq_mchkey: ""
      }
    };
  },
  mounted() {
    this._getGood();
    this._getUpdate();
  },
  methods: {
    addPay() {
      this.$axios({
        url: api.pay,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          edit: true,
          alipay: this.ruleForm.alipay,
          wxpay: this.ruleForm.wxpay,
          qqpay: this.ruleForm.qqpay,
          epayurl: this.ruleForm.epayurl,
          epayid: this.ruleForm.epayid,
          epaykey: this.ruleForm.epaykey,
          codeid: this.ruleForm.codeid,
          codekey: this.ruleForm.codekey,
          ali_partner: this.ruleForm.ali_partner,
          ali_seller: this.ruleForm.ali_seller,
          ali_key: this.ruleForm.ali_key,
          f2f_appid: this.ruleForm.f2f_appid,
          f2f_public: this.ruleForm.f2f_public,
          f2f_private: this.ruleForm.f2f_private,
          wx_appid: this.ruleForm.wx_appid,
          wx_mchid: this.ruleForm.wx_mchid,
          wx_key: this.ruleForm.wx_key,
          wx_secret: this.ruleForm.wx_secret,
          qq_mchid: this.ruleForm.qq_mchid,
          qq_mchkey: this.ruleForm.qq_mchkey
        })
      }).then(res => {
        if (res.data.status == 200) {
          this.$message({
            message: res.data.message,
            type: "success"
          });
        } else {
          this.$message.error(res.data.message);
        }
      });
    },

    _getGood() {
      this.$axios({
        url: api.pay,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          all: true
        })
      }).then(res => {
        this.ruleForm = res.data.data;
      });
    },
    //获取更新
    _getUpdate() {
      this.$axios({
        url: api.update
      }).then(res => {
        this.update = res.data.data;
      });
    }
  }
};
</script>

<style scoped>
.el-breadcrumb {
  line-height: 40px;
}
.demo-ruleForm {
  margin-top: 20px;
}
.explain {
  width: 100%;
  padding: 8px 16px;
  margin: 0;
  border-radius: 4px;
  background-color: #f0f9eb;
  color: #67c23a;
}
.title{
  font-size: 18px;
}
</style>


