<template>
  <div>
    <div class="content-page conn">
      <div class="content">
        <div class="container">
          <div class="row" style="padding:0 10px">
            <div class="navhead">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/admin/index' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item v-if="!type">商品管理</el-breadcrumb-item>
                <el-breadcrumb-item v-if="type" :to="{ path: '/admin/goods' }">商品管理</el-breadcrumb-item>
                <el-breadcrumb-item v-if="type">{{name}}</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12">
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h3 class="panel-title">
                    <font>商品管理</font>
                    <el-button
                      type="primary"
                      @click="$router.push('/admin/addgoods')"
                      size="mini"
                      style="float:right"
                      round
                    >添加商品</el-button>
                  </h3>
                </div>
                <div class="panel-body">
                  <el-table
                    v-loading="loading"
                    :data="goodsList.filter(data => !search || data.name.toLowerCase().includes(search.toLowerCase()))"
                    highlight-current-row
                    style="width: 100%"
                  >
                    <el-table-column type="index" width="50"></el-table-column>
                    <el-table-column property="name" label="商品名称" width="200"></el-table-column>
                    <el-table-column property="price" label="商品价格" width="80"></el-table-column>
                    <el-table-column property="sales" label="商品销量" width="80"></el-table-column>
                    <el-table-column property="weight" label="商品权重" width="80"></el-table-column>
                    <el-table-column label="添加时间" width="160">
                      <template slot-scope="scope">
                        <span>{{ scope.row.time | date }}</span>
                      </template>
                    </el-table-column>

                    <el-table-column label="状态" width="80">
                      <template slot-scope="scope">
                        <span
                          v-if="scope.row.state==1"
                          style="color:green"
                        >{{ scope.row.state | type }}</span>
                        <span v-else style="color:red">{{ scope.row.state | type }}</span>
                      </template>
                    </el-table-column>
                    <el-table-column labelalign="right" width="220">
                      <template slot="header" slot-scope="scope">
                        <el-input v-model="search" size="mini" placeholder="输入名称搜索" />
                      </template>
                      <template slot-scope="scope">
                        <el-button
                          size="mini"
                          type="success"
                          @click="$router.push('/admin/kmlist?id='+scope.row.id)"
                        >卡密</el-button>
                        <el-button
                          size="mini"
                          type="primary"
                          @click="goodsEdit(scope.$index, scope.row)"
                        >编辑</el-button>
                        <el-button
                          size="mini"
                          type="danger"
                          @click="goodsDel(scope.$index, scope.row)"
                        >删除</el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                </div>

                <!-- 编辑 -->
                <div
                  class="modal fade"
                  id="myEdit"
                  tabindex="-1"
                  role="dialog"
                  aria-labelledby="myModalLabel"
                >
                  <div class="modal-dialog" role="document">
                    <div class="modal-content">
                      <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                          <span aria-hidden="true">&times;</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel">编辑商品</h4>
                      </div>
                      <div class="modal-body">
                       <div class="form-group">
                            <label>商品名称:</label>
                            <el-input v-model="goodsInfo.name" placeholder="请输入名称"></el-input>
                          </div>
                          <div class="form-group">
                            <label style="display:block">商品分类:</label>
                            <el-select v-model="goodsInfo.type_id" placeholder="请选择分类">
                              <el-option
                                v-for="item in typeList"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id"
                              ></el-option>
                            </el-select>
                          </div>
                          <div class="form-group">
                            <label style="display:block">商品价格</label>
                            <el-input-number v-model="goodsInfo.price" :min="0"></el-input-number>
                          </div>
                          <div class="form-group">
                            <label style="display:block">分类权重(值越大排序越靠前)</label>
                            <el-input-number v-model="goodsInfo.weight" :min="0"></el-input-number>
                          </div>
                          <div class="form-group">
                            <label style="display:block">起购数量</label>
                            <el-input-number v-model="goodsInfo.minbuy" :min="0"></el-input-number>
                          </div>
                          <div class="form-group">
                            <label style="display:block">限购数量</label>
                            <el-input-number v-model="goodsInfo.maxbuy" :min="0"></el-input-number>
                          </div>
                          <div class="form-group">
                            <label style="display:block">当前销量</label>
                            <el-input-number v-model="goodsInfo.sales" :min="0"></el-input-number>
                          </div>
                          
                         

                          <div class="form-group">
                            <label style="display:block">商品介绍</label>
                            <el-input
                              type="textarea"
                              :rows="5"
                              placeholder="请输入内容,支持html"
                              v-model="goodsInfo.info"
                            ></el-input>
                          </div>
                          <div class="form-group">
                            <label style="display:block">商品图片</label>
                            <el-upload
                              class="upload-demo"
                              :action="action"
                              :data="data"
                              :on-error="imgError"
                              :on-success="imgSuccess"
                              :before-remove="beforeRemove"
                              multiple
                              :limit="1"
                              :on-exceed="handleExceed"
                              :file-list="fileList"
                            >
                              <el-button size="small" type="primary">点击上传</el-button>
                            </el-upload>
                          </div>
                          <div class="form-group">
                            <label>是否启用:</label>
                            <el-switch
                              v-model="goodsInfo.state"
                              active-color="#13ce66"
                              inactive-color="#ccc"
                              active-value="1"
                              inactive-value="0"
                            ></el-switch>
                          </div>
                       
                      </div>
                      <div class="modal-footer text-center">
                        <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
                        <button type="button" class="btn btn-primary" @click="setGoods">确认</button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import { Toast, Dialog } from "vant";
import qs from "querystring";
export default {
  data() {
    return {
      data: { key: "" },
      goodsList: [],
      goodsInfo: [],
      search: "",
      fileList: [],
      action: "",
      typeList: [],
      type: "",
      name: "",
     
      loading: true
    };
  },
  filters: {
    //时间戳转日期
    date: function(time) {
      var date = new Date(time * 1000); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate() + " ";
      var h = date.getHours() + ":";
      var m = date.getMinutes() + ":";
      var s = date.getSeconds();
      return Y + M + D + h + m + s;
    },
    //状态
    type: function(state) {
      if (state == 0) {
        return "已下架";
      } else {
        return "正常";
      }
    }
  },
  mounted() {
    this.action = api.upload;
    this.type = this.$route.query.type_id;
    if (this.$route.query.type_id) {
      this._getOneType(this.$route.query.type_id);
      this._getGood(this.$route.query.type_id);
    } else {
      this._getGood();
    }

    this.getType();
    this._getUpkey();
  },
  methods: {
    //上传失败
    imgError(err, file, fileList) {
      this.$message.error(err);
    },
    imgSuccess(response, file, fileList) {
      if (response.status == 200) {
        this.$message({
          message: response.message,
          type: "success"
        });
        this.goodsInfo.img = response.data;
      } else {
        this.$message.error(response.message);
        this.fileList = [];
      }
    },

    handleExceed(files, fileList) {
      this.$message.error("上传数量超过限制");
    },
    beforeRemove(file, fileList) {
      return this.$message({
        message: "删除成功",
        type: "success"
      });
    },
    //提交编辑
    setGoods() {
      if (!this.goodsInfo.name) {
        this.$message.error("商品名称不能为空");
      } else if (!this.goodsInfo.type_id) {
        this.$message.error("请选择商品分类");
      } else {
        this.$axios({
          url: api.goods,
          method: "post",
          data: qs.stringify({
            username: sessionStorage.getItem("adminuser"),
            password: sessionStorage.getItem("adminpass"),
            edit: this.goodsInfo.id,
            name: this.goodsInfo.name,
            type_id: this.goodsInfo.type_id,
            price: this.goodsInfo.price,
            minbuy: this.goodsInfo.minbuy,
            maxbuy: this.goodsInfo.maxbuy,
            weight: this.goodsInfo.weight,
            state: this.goodsInfo.state,
            sales: this.goodsInfo.sales,
            img: this.goodsInfo.img,
            info: this.goodsInfo.info
          })
        }).then(res => {
          if (res.data.status == 200) {
            this.$message({
              message: res.data.message,
              type: "success"
            });
          } else {
            this.$message.error(res.data.message);
          }
          $("#myEdit").modal("hide");
          this._getGood();
          this.fileList = [];
        });
      }
    },
    // 获取单个分类信息
    goodsEdit(index, row) {
      this.$axios({
        url: api.goods,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          id: row.id
        })
      }).then(res => {
        this.goodsInfo = res.data.data;
        this.goodsInfo.type_id = "";
       
        $("#myEdit").modal("show");
      });
    },
    //  删除单个商品
    goodsDel(index, row) {
      Dialog.confirm({
        title: "确认删除",
        message: "删除后无法恢复,是否继续?"
      })
        .then(() => {
          this.$axios({
            url: api.goods,
            method: "post",
            data: qs.stringify({
              username: sessionStorage.getItem("adminuser"),
              password: sessionStorage.getItem("adminpass"),
              del: row.id
            })
          }).then(res => {
            if (res.data.status == 200) {
              this.$message({
                message: res.data.message,
                type: "success"
              });
            } else {
              this.$message.error(res.data.message);
            }
            this._getGood();
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    //获取商品
    _getGood(type) {
      if (!type) {
        this.$axios({
          url: api.goods,
          method: "post",
          data: qs.stringify({
            username: sessionStorage.getItem("adminuser"),
            password: sessionStorage.getItem("adminpass"),
            all: true
          })
        }).then(res => {
          let compare = function(obj1, obj2) {
            if (obj1.weight > obj2.weight) {
              return -1;
            } else if (obj1.weight < obj2.weight) {
              return 1;
            } else {
              return 0;
            }
          };

          this.goodsList = res.data.data.sort(compare);
          this.loading = false;
        });
      } else {
        this._getTypeGood(type);
      }
    },
    //指定分类商品
    _getTypeGood(id) {
      this.$axios({
        url: api.goods,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          type: id
        })
      }).then(res => {
        let compare = function(obj1, obj2) {
          if (obj1.weight > obj2.weight) {
            return -1;
          } else if (obj1.weight < obj2.weight) {
            return 1;
          } else {
            return 0;
          }
        };

        this.goodsList = res.data.data.sort(compare);
        this.loading = false;
      });
    },
    //获取分类
    getType() {
      this.$axios({
        url: api.type,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          all: true
        })
      }).then(res => {
        this.typeList = res.data.data;
      });
    },
    //获取当前分类
    _getOneType(id) {
      this.$axios({
        url: api.type,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          id: id
        })
      }).then(res => {
        this.name = res.data.data.name;
      });
    },
    //获取图片上传key
    _getUpkey() {
      this.$axios({
        url: api.config,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          all: true
        })
      }).then(res => {
        this.data.key = res.data.data.upkey;
      });
    }
  }
};
</script>


<style scoped>
.el-breadcrumb {
  line-height: 40px;
}
</style>


