<template>
  <div class="bf_mobile">
    <div class="bf_head">
      <h1 @click="$router.push('/')">{{data.name}}</h1>
      <el-button type="primary" plain round class="bf_cx" size="medium" @click="$router.push('/')">重新选购</el-button>
    </div>

    <div class="bf_shopImg">
      <div class="bf_shopcuxiao" v-if="cuxiao">
        <div class="bf_tempbg">
          <div class="bf_cuxiaosm">距结束</div>

          <span :endTime="endTime" :endText="endText" id="timer">
            <slot>{{content}}</slot>
          </span>
          <div class="bf_daoshou">
            <em style="font-size:14px">到手价 ￥</em>
            {{cuxiao_price}}
          </div>
        </div>
      </div>
      <img :src="`/upload/${goods.img}`" class="bf_imgone" alt />
      <div class="bf_shop_name">{{goods.name}}</div>
      <div class="bf_shop_price" :class="cuxiao?'shop_ori':''">
        <em style="font-size:14px">￥</em>
        {{goods.price}}
      </div>
    </div>
    <!-- 库存 -->
    <div class="bf_parameter">
      <div class="bf_shopstock">
        <span class="bf_spname">库存</span>
        <h4 class="bf_h4" v-if="data.stockno==2">{{stock}}</h4>
        <h4 class="bf_h4" v-else>{{stock | fter}}</h4>
        <h5 class="bf_h5">销量 {{goods.sales}}</h5>
      </div>
      <div class="bf_shopstock" v-if="goods.sm1 || goods.sm2 || goods.sm3">
        <span class="bf_spname">服务</span>
        <h4 class="bf_h4">
          <van-tag type="primary" v-if="goods.sm1">{{goods.sm1}}</van-tag>
          <van-tag type="success" v-if="goods.sm2">{{goods.sm2}}</van-tag>
          <van-tag plain type="primary" v-if="goods.sm3">{{goods.sm3}}</van-tag>
        </h4>
      </div>
    </div>
    <!-- 购买数量 -->
    <div class="bf_parameter">
      <div class="bf_buynum">
        <span class="bf_buyname">购买数量</span>
        <em style="font-size:12px;color:red">{{goods.minbuy}}件起购，限购{{goods.maxbuy}}件</em>
        <el-input-number size="mini" v-model="num" :step="1" :min="1" style="float: right;margin:6px 0"></el-input-number>
      </div>
      <div class="bf_buynum">
        <span class="bf_buyname">联系方式</span>
        <el-input
          size="mini"
          placeholder="请输入QQ号，作为购买凭证"
          style="float: right;width:70%"
          v-model="qq"
        ></el-input>
      </div>
    </div>
    <!-- 商品介绍 -->
    <div class="bf_shopdetail">
      <div class="bf_detailtitle">商品说明</div>
      <div v-html="goods.info" class="bf_shopcon"></div>
    </div>
    <div class="bf_footer">
      <div v-html="data.link" class="bf_link"></div>
      <div v-html="data.copy" class="bf_copy"></div>
    </div>
    <!-- 支付 -->
    <div class="bf_pay">
      <van-action-sheet v-model="show" cancel-text="取消" @cancel="onCancel">
        <button class="bf_pay_con" @click="setPay('alipay')" v-if="pay.alipay != 0">
          <i class="bf_icon alipay"></i> 支付宝支付
        </button>
        <button class="bf_pay_con" @click="setPay('wxpay')" v-if="pay.wxpay != 0">
          <i class="bf_icon wxpay"></i> 微信支付
        </button>
        <button
          class="bf_pay_con"
          style="border-bottom:0"
          @click="setPay('qqpay')"
          v-if="pay.qqpay != 0"
        >
          <i class="bf_icon qqpay"></i> QQ支付
        </button>
      </van-action-sheet>
    </div>

    <van-dialog
      v-model="payno"
      title="订单确认"
      confirmButtonText="确认支付"
      cancelButtonText="取消支付"
      @confirm="onConfirm"
      show-cancel-button
    >
      <div class="bf_goBuy">
        <div class="bf_pay_list">订单编号 : {{this.orderInfo.out_trade_no}}</div>
        <div class="bf_pay_list">商品名称 : {{this.orderInfo.gName}}</div>
        <div class="bf_pay_list">商品单价 : {{this.goods.price}}</div>
        <div class="bf_pay_list">购买数量 : {{this.num}}</div>
        <div class="bf_pay_list">联系方式 : {{this.qq}}</div>
        <div class="bf_pay_list">实付金额 : {{this.orderInfo.zfPrice}}</div>
        <div class="bf_pay_list">付款方式 : {{this.orderInfo.sm}}</div>
      </div>
    </van-dialog>

    <div class="bf_buybtn">
      <van-goods-action>
        <van-goods-action-icon icon="chat-o" text="客服" @click="kf" />
        <van-goods-action-icon icon="sign" text="查单" @click="$router.push('/query')"/>
        <van-goods-action-button v-if="stock==0" type="danger" text="库存不足" disabled />
        <van-goods-action-button v-else type="danger" text="立即购买" @click="buyBtn" />
      </van-goods-action>
    </div>
  </div>
</template>

<script>
import { Toast, Dialog, Tag } from "vant";
import api from "@/api";
import qs from "querystring";
//时间戳转日期
function date(time) {
  var date = new Date(time * 1000); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
  var Y = date.getFullYear() + "";
  var M =
    (date.getMonth() + 1 < 10
      ? "" + (date.getMonth() + 1)
      : date.getMonth() + 1) + "";
  var D = date.getDate() + "";
  var h = date.getHours() + ":";
  var m = date.getMinutes() + ":";
  var s = date.getSeconds();
  return M + "月" + D + "日" + h + m + s;
}

export default {
  data() {
    return {
      goodsid: "",
      goods: {},
      stock: "",
      pro: [],
      data: [],
      cuxiao: false,
      cuxiao_price: "",
      content: "",
      show: false,
      payno: false,
      pay: {},
      num: "",
      qq: "",
      orderInfo: {
        out_trade_no: "",
        gName: "",
        price: "",
        zfPrice: "",
        gType: "",
        sm: ""
      }
    };
  },
  props: {
    endTime: {
      type: String,
      default: ""
    },
    endText: {
      type: String,
      default: "活动已结束"
    }
  },
  filters: {
    //库存
    fter: function(num) {
      if (num == 0) {
        return "空";
      } else if (num <= 5) {
        return "少量";
      } else if (num <= 10) {
        return "一般";
      } else if (num <= 30) {
        return "很多";
      } else {
        return "充足";
      }
    }
  },
  mounted() {
    this._getSet();
    this.goodsid = this.$route.params.id;
    this._getGoods(this.$route.params.id);
  },
  methods: {
    //确认支付
    onConfirm() {
      window.location.href = api.api +
        "/includes/pay/submit.php?type=" +
        this.orderInfo.gType +
        "&out_trade_no=" +
        this.orderInfo.out_trade_no +
        "&name=" +
        this.orderInfo.gName +
        "&money=" +
        this.orderInfo.zfPrice +
        "&goodsid=" +
        this.goods.id +
        "&num=" +
        this.num+"&qq="+this.qq;

      this.orderInfo = {};
      this.payno = false;
      this.show = false;
    },
    //订单确认
    setPay(type) {
      let dates = new Date().getTime();
      this.orderInfo.out_trade_no = this.order(dates);
      this.orderInfo.gName = this.goods.name;
      this.orderInfo.price = this.goods.price;
      this.orderInfo.gType = type;
      if (this.cuxiao) {
        //计算金额
        this.orderInfo.zfPrice = this.cuxiao_price * this.num;
      } else {
        this.orderInfo.zfPrice = this.goods.price * this.num;
      }
      if (type == "alipay") {
        //支付说明
        this.orderInfo.sm = "支付宝";
      } else if (type == "wxpay") {
        this.orderInfo.sm = "微信支付";
      } else {
        this.orderInfo.sm = "QQ支付";
      }
      this.payno = true;
    },
    //取消支付
    onCancel() {
      this.show = false;
    },
    //联系客服
    kf() {
      Dialog.confirm({
        // title: '标题',
        message: "客服QQ: " + this.data.kf + " 点击确定联系"
      })
        .then(() => {
          window.location.href =
            "http://wpa.qq.com/msgrd?v=3&uin=" +
            this.data.kf +
            "&site=qq&menu=yes";
        })
        .catch(() => {
          // on cancel
        });
    },
    //购买按钮
    buyBtn() {
      let reg = /^[0-9]{6,11}$/;
      let qq = reg.test(this.qq);
      if (!this.num) {
        Toast.fail("请输入购买数量");
      } else if (this.num < this.goods.minbuy) {
        Toast.fail("此商品最低购买" + this.goods.minbuy + "件");
      } else if (this.num > this.goods.maxbuy) {
        Toast.fail("此商品最多购买" + this.goods.maxbuy + "件");
      } else if (this.num > this.stock) {
        Toast.fail("库存不足,请减少购买数量");
      } else if (!this.qq) {
        Toast.fail("请填写联系方式");
      } else if (!qq) {
        Toast.fail("请填写正确的联系方式");
      } else {
        this.show = true;
      }
    },
    //获取商品
    _getGoods(id) {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          goodsid: id
        })
      }).then(res => {
        if (!res.data.data) {
          Dialog.alert({
            message: "商品不存在"
          }).then(() => {
            this.$router.push("/");
          });
        }
        this.goods = res.data.data;
        this.num = res.data.data.minbuy;
        this._getStock(id); //获取库存
      });
    },
    //获取库存
    _getStock(id) {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          stock: id
        })
      }).then(res => {
        this.stock = res.data.data;
        //获取支付方式
        this._getPayType();
        //获取折扣
        this._getPro(id);
      });
    },
    //获取部分设置
    _getSet() {
      this.$axios({
        url: api.site
      })
        .then(res => {
          this.data = res.data.data;
        })
        .catch(err => {});
    },
    //获取折扣
    _getPro(id) {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          pro: id
        })
      }).then(res => {
        this.pro = res.data.data;
        if (res.data.data) {
          var timestamp = Date.parse(new Date());
          if (res.data.data.endtime > timestamp / 1000) {
            this.cuxiao = true;
            this.cuxiao_price = (
              (res.data.data.discount / 10) *
              this.goods.price
            ).toFixed(2);
            this.countdowm(res.data.data.endtime);

            //弹窗提示
            this.$notify({
              title: "促销说明",
              dangerouslyUseHTMLString: true,
              duration: 8000,
              offset: 50,
              message:
                '该商品正在参加<span style="color:red">' +
                res.data.data.name +
                '</span>活动 <span style="color:red">' +
                date(res.data.data.endtime) +
                "</span>前购买可享受" +
                res.data.data.discount +
                '折优惠，预计到手价<span style="color:red">' +
                this.cuxiao_price +
                "元</span>"
            });
          }
        }
      });
    },
    //获取支付方式
    _getPayType() {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          pay: true
        })
      }).then(res => {
        this.pay = res.data.data;
      });
    },
    //生成订单号
    order(timestamp) {
      //时间
      var date = new Date(timestamp); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      var Y = date.getFullYear();
      var M =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var D = date.getDate();
      var h = date.getHours();
      var m = date.getMinutes();
      var s = date.getSeconds();
      //后四位随机数
      let str = "0123456789";
      let num = "";
      for (let i = 0; i < 4; i++) {
        num += str[parseInt(Math.random() * str.length)];
      }
      return Y + M + D + h + m + s + num;
    },
    //计算时间
    countdowm(timestamp) {
      let self = this;
      let timer = setInterval(function() {
        let nowTime = new Date();
        let endTime = new Date(timestamp * 1000);
        let t = endTime.getTime() - nowTime.getTime();
        if (t > 0) {
          let day = Math.floor(t / 86400000);
          let hour = Math.floor((t / 3600000) % 24);
          let min = Math.floor((t / 60000) % 60);
          let sec = Math.floor((t / 1000) % 60);
          hour = hour < 10 ? "0" + hour : hour;
          min = min < 10 ? "0" + min : min;
          sec = sec < 10 ? "0" + sec : sec;
          let format = "";
          if (day > 0) {
            format = `${day}:${hour}:${min}:${sec}`;
          }
          if (day <= 0 && hour > 0) {
            format = `${hour}:${min}:${sec}`;
          }
          if (day <= 0 && hour <= 0) {
            format = `${min}:${sec}`;
          }
          self.content = format;
        } else {
          clearInterval(timer);
          self.content = self.endText;
          self._callback();
        }
      }, 1000);
    },
    _callback() {
      if (this.callback && this.callback instanceof Function) {
        this.callback(...this);
      }
    }
  }
};
</script>

<style lang="less" scoped>
@import '../../../assets/css/reset.css';
.bf_mobile {
  width: 100%;
  overflow: hidden;
  position: relative;
  background: #f8f8f8;
  .bf_head {
    height: 50px;
    background: #fff;
    border-bottom: 1px solid #ccc;
    box-shadow: 1px 1px 5px #888888;
    padding: 0 10px;
    h1 {
      display: inline-block;
      font-size: 20px;
      line-height: 50px;
    }
    .bf_cx {
      float: right;
      margin-top: 7px;
    }
  }
  .bf_shopImg {
    height: 400px;
    background: #fff;
    position: relative;
    margin-bottom: 10px;
    .bf_shopcuxiao {
      position: absolute;
      width: 100%;
      bottom: 60px;
      height: 60px;
      // background: rgba(53, 47, 47, 0.8);
      background: linear-gradient(to right, #5769f9, #d658fe);
      .bf_tempbg {
        position: relative;
        height: 60px;
        .bf_cuxiaosm {
          position: absolute;
          left: 10px;
          top: 7px;
          display: inline-block;
          font-size: 15px;
          color: #fff;
        }
        #timer {
          position: absolute;
          left: 10px;
          bottom: 7px;
          display: inline-block;
          font-size: 20px;
          color: #fff;
        }
        .bf_daoshou {
          position: absolute;
          right: 5px;
          margin: 10px;
          font-size: 40px;
          color: red;
        }
      }
    }
    .bf_imgone {
      width: 100%;
      height: 340px;
    }
    .bf_shop_name {
      display: inline-block;
      width: 70%;
      font-size: 16px;
      font-weight: 600;
      margin: 10px;
      display: -webkit-box;
      -webkit-line-clamp: 2; /* 行数 */
      -webkit-box-orient: vertical;
      overflow: hidden;
    }
    .bf_shop_price {
      position: absolute;
      right: 10px;
      bottom: 10px;
      font-size: 30px;
      color: red;
    }
    .bf_shop_ori {
      color: #999;
    }
  }
  .bf_parameter {
    background: #fff;
    margin-bottom: 10px;
    padding: 5px 10px;
    .bf_shopstock {
      height: 30px;
      line-height: 30px;
      .bf_spname {
        font-size: 14px;
        color: #888;
      }
      .bf_h4,
      .bf_h5 {
        display: inline-block;
        margin-left: 20px;
        font-size: 14px;
        line-height: 30px;
      }
      .bf_h5 {
        float: right;
      }
    }
    .bf_buynum {
      height: 40px;
      line-height: 40px;
      .bf_buyname {
        font-size: 16px;
        color: #888;
      }
    }
  }
  .bf_pay {
    .bf_pay_con {
      font-size: 20px;
      height: 50px;
      line-height: 50px;
      display: block;
      width: 100%;
      padding: 0;
      font-size: 20px;
      line-height: 50px;
      text-align: center;
      background-color: #fff;
      border: none;
      cursor: pointer;
      border-bottom: 0.5px solid #ccc;
      .bf_icon {
        display: inline-block;
        width: 18px;
        height: 18px;
      }
      .alipay {
        background: url("../../../assets/images/alipay.svg");
        background-size: cover;
      }
      .wxpay {
        background: url("../../../assets/images/wxpay.svg");
        background-size: cover;
      }
      .qqpay {
        background: url("../../../assets/images/qqpay.svg");
        background-size: cover;
      }
    }
  }
  .bf_shopdetail {
    padding: 10px;
    background: #fff;
    margin-bottom: 10px;
    .bf_detailtitle {
      height: 28px;
      line-height: 28px;
      font-size: 15px;
      border-bottom: 0.5px solid #ccc;
    }
    .bf_shopcon {
      margin: 12px 0;
    }
  }
  .bf_footer {
    margin-bottom: 50px;
    padding: 10px;
    background: #fff;
    .bf_link {
      text-align: center;
      font-size: 14px;
      margin-top: 10px;
    }
    .bf_copy {
      text-align: center;
      font-size: 14px;
      margin-top: 10px;
      margin-bottom: 10px;
    }
  }
  
  .bf_goBuy {
    padding: 10px 20px;
    .bf_pay_list {
      height: 30px;
      line-height: 30px;
    }
  }
}
</style>


