<template>
  <div id="bf_pc">
    <div class="bf_topbar">
      <div class="bf_main">
        <div class="bf_topbar_left">欢迎使用{{data.name}}</div>
        <div class="bf_topbar_right">
          <a @click="$router.push('/')">返回首页</a>
          <a @click="kf">联系客服</a>
        </div>
      </div>
    </div>

    <div class="bf_midbar">
      <div class="bf_main">
        <div class="bf_midbar_left">
          <a href="/">
            <img :src="`/upload/${data.logo}`" alt />
          </a>
        </div>
        <div class="bf_midbar_right">
          <div class="bf_search">
            <el-input placeholder="请输入商品名称" v-model="goodsquery" class="input-with-select">
              <el-button slot="append" icon="el-icon-search" @click="gQuery"></el-button>
            </el-input>
            <ul>
              <li style="border-left:0;padding-left:0" @click="$router.push('/')">全部商品</li>
              <li
                v-for="(item,index) in allList"
                :key="item.id"
                v-if="index<data.rec"
                @click="$router.push('/goods/'+item.id+'.html')"
              >{{item.name}}</li>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <div class="bf_navbar">
      <div class="bf_main">
        <ul>
          <li>
            <a @click="$router.push('/')">全部</a>
          </li>
          <li v-for="item in typeList" :key="item.id">
            <a @click="$router.push('/?typeId='+item.id)">{{item.name}}</a>
          </li>
        </ul>
      </div>
    </div>

    <div class="bf_goodsbox">
      <div class="bf_main">
        <div class="bf_query">
          <el-input placeholder="请输入商户单号/联系方式" v-model="query" class="input-with-select">
            <el-button slot="append" icon="el-icon-search" @click="onQuery"></el-button>
          </el-input>
        </div>

        <div class="bf_orderlog">
          <el-table :data="order" border style="width: 100%">
            <el-table-column label="订单时间" width="160">
              <template slot-scope="scope">
                <span>{{ scope.row.endtime | date }}</span>
              </template>
            </el-table-column>
            <el-table-column prop="out_trade_no" label="订单编号" width="180"></el-table-column>
            <el-table-column prop="gName" label="商品名称" width="180"></el-table-column>
            <el-table-column label="卡密" width="216">
              <template slot-scope="scope">
                <span>{{ scope.row.km}}</span>
                <el-button
                  size="small"
                  v-clipboard:copy="scope.row.km"
                  v-clipboard:success="onCopy"
                  v-clipboard:error="onError"
                >点击复制</el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </div>
    </div>

    <div class="bf_footer">
      <div class="bf_footer_cont">
        <div v-html="data.link"></div>
        <div v-html="data.copy" class="bf_copy"></div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import qs from "querystring";
export default {
  data() {
    return {
      query: "",
      order: [],
      data: {},
      allList: [],
      typeList: [],
      goodsquery: ""
    };
  },
  filters: {
    //时间戳转日期
    date: function(time) {
      var date = new Date(time * 1000); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate() + " ";
      var h = date.getHours() + ":";
      var m = date.getMinutes() + ":";
      var s = date.getSeconds();
      return Y + M + D + h + m + s;
    }
  },
  mounted() {
    this._getSet();
    if (this.$route.query.on) {
      this.query = this.$route.query.on;
      this.onQuery();
    }
    this._getType();
  },
  methods: {
    onCopy(e) {
      this.$message({
        message: "复制成功",
        type: "success"
      });
    },
    onError(e) {
      this.$message.error("复制失败");
    },
    //查询订单
    onQuery() {
      if (!this.query) {
        this.$message.error("请输入订单号或联系方式");
      } else {
        this.$axios({
          url: api.site,
          method: "post",
          data: qs.stringify({
            query: this.query
          })
        }).then(res => {
          if (res.data.status == 200) {
            this.$message({
              message: res.data.message,
              type: "success"
            });
            this.order = res.data.data;
          } else {
            this.$message.error(res.data.message);
          }
        });
      }
    },

    //商品搜索
    gQuery() {
      if (!this.goodsquery) {
        this.$message.error("搜索内容不能为空");
      } else {
        this.$router.push("/?goods=" + this.goodsquery);
      }
    },

    //联系客服
    kf() {
      this.$confirm("客服QQ: " + this.data.kf + " 点击确定立即联系", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "success"
      })
        .then(() => {
          window.location.href =
            "http://wpa.qq.com/msgrd?v=3&uin=" +
            this.data.kf +
            "&site=qq&menu=yes";
        })
        .catch(() => {});
    },
    //获取商品分类
    _getType() {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          type: true
        })
      }).then(res => {
        let compare = function(obj1, obj2) {
          if (obj1.weight > obj2.weight) {
            return -1;
          } else if (obj1.weight < obj2.weight) {
            return 1;
          } else {
            return 0;
          }
        };
        this.typeList = res.data.data.sort(compare);
      });
    },
    //获取全部商品
    _getGoods() {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          goods: true
        })
      }).then(res => {
        let compare = function(obj1, obj2) {
          if (obj1.weight > obj2.weight) {
            return -1;
          } else if (obj1.weight < obj2.weight) {
            return 1;
          } else {
            return 0;
          }
        };
        this.allList = res.data.data;
      });
    },
    //获取部分设置
    _getSet() {
      this.$axios({
        url: api.site
      })
        .then(res => {
          this.data = res.data.data;
          //获取商品
          this._getGoods();
          //是否弹出公告
          if (this.data.querygg) {
            this.$notify({
              title: "网站公告",
              offset: 60,
              message: this.data.querygg
            });
          }
        })
        .catch(err => {});
    }
  },
  watch: {
    typeId(newVal) {
      this._getIdgoods(newVal);
    }
  }
};
</script>

<style lang="less" scoped>
@import '../../../assets/css/reset.css';
#bf_pc {
   background: #fff;
  .bf_topbar {
    width: 100%;
    height: 40px;
    position: relative;
    background: #f2f2f2;
    -webkit-transform: translate3d(0, 0, 0);
    border-bottom: 1px solid #e5e5e5;
    line-height: 40px;
    color: #999;
    font-size: 12px;
    .bf_main {
      margin: 0 auto;
      width: 100%;
      max-width: 1230px;
      .bf_topbar_left {
        float: left;
      }
      .bf_topbar_right {
        float: right;
        a {
          color: #999;
          cursor: pointer;
        }
      }
    }
  }
  .bf_midbar {
    height: 130px;
    background-color: #fff;
    .bf_main {
      margin: 0 auto;
      width: 100%;
      max-width: 1230px;
      .bf_midbar_left {
        float: left;
        height: 130px;
        width: 300px;
        img {
          width: 100%;
          height: 100%;
          object-fit: scale-down;
        }
      }
      .bf_midbar_right {
        float: right;
        max-width: 100%;
        width: 625px;
        .bf_search {
          width: 100%;
          margin: 31px auto;
          ul {
            width: 625px;
            height: 8px;
            overflow: hidden;
            list-style: none;
            padding: 10px 0;
            li {
              float: left;
              display: inline-block;
              border-left: 1px solid #ccc;
              line-height: 1.1;
              padding: 0 12px;
              font-size: 16px;
              color: #888;
              cursor: pointer;
            }
          }
        }
      }
    }
  }
  .bf_navbar {
    background-color: #ff4500;
    height: 40px;
    .bf_main {
      margin: 0 auto;
      width: 100%;
      max-width: 1230px;
      overflow: hidden;
      ul {
        width: 1230px;
        overflow: hidden;
        list-style: none;
        li:hover {
          background: #0066ff;
          border-bottom: 1px solid #0066ff;
        }
        li {
          cursor: pointer;
          float: left;
          a {
            display: block;
            font-size: 16px;
            padding: 0 10px;
            font-weight: 700;
            line-height: 38px;
            color: #fff;
          }
        }
      }
    }
  }

  .bf_goodsbox {
    overflow: hidden;
    .bf_main {
      margin: 0 auto;
      width: 100%;
      max-width: 1230px;
      .bf_query {
        margin: 50px auto;
        width: 60%;
      }
      .bf_orderlog {
        width: 60%;
        margin: 0 auto;
      }
    }
  }
  .bf_footer {
    margin-top: 40px;
    border-top: solid 2px #ff4500;
    background-color: #fff;
    padding: 20px 0;
    .bf_footer_cont {
      text-align: center;
      font-size: 12px;
      width: 100%;
      max-width: 1230px;
      margin: 0 auto;
      .bf_copy {
        margin-top: 10px;
      }
    }
  }
}
</style>


