<template>
  <div id="bf_pc">
    <div class="bf_topbar">
      <div class="bf_main">
        <div class="bf_topbar_left">欢迎使用{{data.name}}</div>
        <div class="bf_topbar_right">
          <a @click="$router.push('/query')">订单查询</a>
          <a @click="kf">联系客服</a>
        </div>
      </div>
    </div>
    <!-- logo、搜索 -->
    <div class="bf_midbar">
      <div class="bf_main">
        <div class="bf_midbar_left">
          <a href="/">
            <img :src="`/upload/${data.logo}`" alt />
          </a>
        </div>
        <div class="bf_midbar_right">
          <div class="bf_search">
            <el-input placeholder="请输入商品名称" v-model="goodsquery" class="input-with-select">
              <el-button slot="append" icon="el-icon-search" @click="search"></el-button>
            </el-input>
            <ul>
              <li style="border-left:0;padding-left:0" @click="allShop">全部商品</li>
              <li
                v-for="(item,index) in allList"
                :key="item.id"
                v-if="index<data.rec"
                @click="$router.push('/goods/'+item.id+'.html')"
              >{{item.name}}</li>
            </ul>
          </div>
        </div>
      </div>
    </div>
    <!-- 分类导航 -->
    <div class="bf_navbar">
      <div class="bf_main">
        <ul>
          <li :class="typeId==0?'active':''">
            <a @click="allShop">全部</a>
          </li>
          <li v-for="item in typeList" :key="item.id" :class="typeId==item.id?'active':''">
            <a @click="gType(item.id)">{{item.name}}</a>
          </li>
        </ul>
      </div>
    </div>

    <div class="bf_swiper">
      <div class="bf_main">
        <div class="block">
          <el-carousel height="160px">
            <el-carousel-item v-for="item in imgs" :key="item.id">
              <a :href="item.url" target="_blank">
                <img :src="item.img" class="swipe_img" alt />
              </a>
            </el-carousel-item>
          </el-carousel>
        </div>
        <div class="bf_gg">
          <div class="bf_gg_cont">
            <div class="bf_gg_name">网站公告</div>
            <div class="bf_gg_sm" v-html="data.pcgg"></div>
          </div>
        </div>
      </div>
    </div>

    <div class="bf_goodsbox">
      <div class="bf_main">
        <!-- <div class="bf_goods_title">
            <a @click="allShop">全部商品</a>
           
        </div>-->
        <div
          v-if="goodsList == ''"
          style="text-align:center;height:100px;line-height:100px;font-size:20px"
        >此分类没有商品哦</div>
        <div
          class="bf_goods_nav"
          v-for="(item,index) in goodsList"
          :key="index"
          @click="$router.push('/goods/'+item.id+'.html')"
        >
          <div class="bf_goods_main">
            <img :src="`/upload/${item.img}`" class="bf_goodsimg" alt />

            <div class="bf_goodsname">{{item.name}}</div>
            <div class="bf_goodsprice">
              <em style="font-size:14px">￥</em>
              {{item.price}}
            </div>
            <div class="bf_goods_foot">
              <el-tag size="mini">销量{{item.sales}}</el-tag>
              <div v-if="item.sm1 || item.sm2 || item.sm3" style="display: inline-block;">
                <el-tag type="success" size="mini" v-if="item.sm1">{{item.sm1}}</el-tag>
                <el-tag type="danger" size="mini" v-if="item.sm2">{{item.sm2}}</el-tag>
              </div>
              <el-tag v-else type="success" size="mini">自动发货</el-tag>
            </div>
            <!-- <div class="bf_goods_tool">
                <div class="bf_sales" style="border-right: solid 1px #ddd;">销量{{item.sales}}</div>
                <div class="bf_sales">销量</div>
            </div>-->
          </div>
        </div>
      </div>
    </div>

    <div class="bf_footer">
      <div class="bf_footer_cont">
        <div v-html="data.link"></div>
        <div v-html="data.copy" class="bf_copy"></div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import qs from "querystring";
import { Toast, Dialog } from "vant";
export default {
  data() {
    return {
      typeId: 0,
      goodsquery: "",
      imgs: [],
      data: {},
      goodsList: [],
      allList: [],
      typeList: []
    };
  },
  mounted() {
    Toast.loading({
      message: "商品加载中...",
      forbidClick: true
    });
    this._getSet();
    this._getImgs();
    this._getType();
  },
  methods: {
    //商品搜索
    gQuery() {
      if (!this.goodsquery) {
        this.$message.error("搜索内容不能为空");
      } else {
        this.$axios({
          url: api.site,
          method: "post",
          data: qs.stringify({
            goodsquery: this.goodsquery
          })
        }).then(res => {
          if (res.data.data == "") {
            this.$message.error("没有此商品哦");
          } else {
            this.$message({
              message: "查询成功",
              type: "success"
            });
            this.goodsList = res.data.data;
          }
        });
      }
    },
    search() {
      if (!this.goodsquery) {
        this.$message.error("搜索内容不能为空");
      } else {
        window.location.href = "/?goods=" + this.goodsquery;
      }
    },
    //全部商品
    allShop() {
      this.goodsList = this.allList;
      this.typeId = 0;
    },
    //分类选择
    gType(id) {
      this.typeId = id;
    },
    //获取轮播图
    _getImgs() {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          swiper: true
        })
      })
        .then(res => {
          this.imgs = res.data.data;
        })
        .catch(err => {});
    },
    //联系客服
    kf() {
      this.$confirm("客服QQ: " + this.data.kf + " 点击确定立即联系", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "success"
      })
        .then(() => {
          window.location.href =
            "http://wpa.qq.com/msgrd?v=3&uin=" +
            this.data.kf +
            "&site=qq&menu=yes";
        })
        .catch(() => {});
    },
    //获取商品分类
    _getType() {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          type: true
        })
      }).then(res => {
        let compare = function(obj1, obj2) {
          if (obj1.weight > obj2.weight) {
            return -1;
          } else if (obj1.weight < obj2.weight) {
            return 1;
          } else {
            return 0;
          }
        };
        this.typeList = res.data.data.sort(compare);
      });
    },
    //获取全部商品
    _getGoods() {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          goods: true
        })
      }).then(res => {
        let compare = function(obj1, obj2) {
          if (obj1.weight > obj2.weight) {
            return -1;
          } else if (obj1.weight < obj2.weight) {
            return 1;
          } else {
            return 0;
          }
        };
        this.goodsList = res.data.data;
        this.allList = res.data.data;
        //判断是否查询
        if (this.$route.query.goods) {
          this.goodsquery = this.$route.query.goods;
          this.gQuery();
        }
        if (this.$route.query.typeId) {
          this.typeId = this.$route.query.typeId;
        }
      });
    },
    //获取指定分类商品
    _getIdgoods(id) {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          id
        })
      }).then(res => {
        this.goodsList = res.data.data;
      });
    },
    //获取部分设置
    _getSet() {
      this.$axios({
        url: api.site
      })
        .then(res => {
          this.data = res.data.data;
          //获取商品
          this._getGoods();
          //是否弹出公告
          if (this.data.gg) {
            this.$alert(this.data.gg, "网站公告", {
              dangerouslyUseHTMLString: true
            });
          }
        })
        .catch(err => {});
    }
  },
  watch: {
    typeId(newVal) {
      this._getIdgoods(newVal);
    }
  }
};
</script>

<style lang="less" scoped>
@import "../../../assets/css/reset.css";
#bf_pc {
  background: #fff;
  .bf_topbar {
    width: 100%;
    height: 40px;
    position: relative;
    background: #f2f2f2;
    -webkit-transform: translate3d(0, 0, 0);
    border-bottom: 1px solid #e5e5e5;
    line-height: 40px;
    color: #999;
    font-size: 12px;
    .bf_main {
      margin: 0 auto;
      width: 100%;
      max-width: 1230px;
      .bf_topbar_left {
        float: left;
      }
      .bf_topbar_right {
        float: right;
        a {
          color: #999;
          cursor: pointer;
        }
      }
    }
  }
  .bf_midbar {
    height: 130px;
    background-color: #fff;
    .bf_main {
      margin: 0 auto;
      width: 100%;
      max-width: 1230px;
      .bf_midbar_left {
        float: left;
        height: 130px;
        width: 300px;
        img {
          width: 100%;
          height: 100%;
          object-fit: scale-down;
        }
      }
      .bf_midbar_right {
        float: right;
        max-width: 100%;
        width: 625px;
        .bf_search {
          width: 100%;
          margin: 31px auto;
          ul {
            width: 625px;
            height: 8px;
            overflow: hidden;
            list-style: none;
            padding: 10px 0;
            li {
              float: left;
              display: inline-block;
              border-left: 1px solid #ccc;
              line-height: 1.1;
              padding: 0 12px;
              font-size: 16px;
              color: #888;
              cursor: pointer;
            }
          }
        }
      }
    }
  }
  .bf_navbar {
    background-color: #ff4500;
    height: 40px;
    .bf_main {
      margin: 0 auto;
      width: 100%;
      max-width: 1230px;
      overflow: hidden;
      ul {
        width: 1230px;
        overflow: hidden;
        list-style: none;
        li:hover {
          background: #0066ff;
          border-bottom: 1px solid #0066ff;
        }
        li {
          cursor: pointer;
          float: left;
          a {
            display: block;
            font-size: 16px;
            padding: 0 10px;
            font-weight: 700;
            line-height: 38px;
            color: #fff;
          }
        }
      }
    }
  }
  .bf_swiper {
    height: 190px;
    .bf_main {
      margin: 0 auto;
      width: 100%;
      height: 185px;
      max-width: 1230px;
      padding-top: 15px;
      .block {
        width: 60%;
        height: 160px;
        float: left;
        .swipe_img {
          width: 100%;
          height: 160px;
          border-radius: 5px;
        }
      }
      .bf_gg {
        display: inline-block;
        float: left;
        width: 38%;
        height: 160px;
        padding-left: 2%;
        .bf_gg_cont {
          border: 1px solid #ccc;
          border-radius: 5px;
          height: 160px;
          .bf_gg_name {
            padding: 6px;
            font-size: 18px;
            font-weight: 600;
            border-bottom: 1px solid #ccc;
          }
          .bf_gg_sm {
            padding: 6px;
            font-size: 14px;
            overflow: hidden;
          }
        }
      }
    }
  }
  .bf_goodsbox {
    overflow: hidden;
    .bf_main {
      margin: 0 auto;
      width: 100%;
      max-width: 1230px;
      .bf_goods_title {
        border: 1px solid #ddd;
        height: 40px;
        font-size: 17px;
        line-height: 40px;
        padding: 0 10px;
        a {
          cursor: pointer;
          font-weight: 600;
        }
      }
      .bf_goods_nav {
        width: 236px;
        height: 325px;
        border: solid 5px transparent;
        border-radius: 2px;
        float: left;
        margin-top: 5px;
        position: relative;
        .bf_goods_main {
          width: 100%;
          height: 325px;
          border: solid 1px #ddd;
          border-radius: 2px;
          background-color: #fff;
          .bf_goodsimg {
            display: block;
            width: 96%;
            height: 240px;
            cursor: pointer;
            margin: 2%;
            object-fit: cover;
            background-repeat: no-repeat;
            border-radius: 2px;
          }
          .bf_goodsname {
            width: 68%;
            height: 45px;
            margin-left: 2%;
            font-size: 16px;
            display: -webkit-box;
            -webkit-line-clamp: 2; /* 行数 */
            -webkit-box-orient: vertical;
            overflow: hidden;
          }
          .bf_goodsprice {
            display: inline-block;
            position: absolute;
            right: 2%;
            top: 250px;
            font-size: 25px;
            color: red;
            font-weight: 600;
          }
          .bf_goods_tool {
            border-top: 1px solid #ddd;
            font-size: 12px;
            height: 36px;
            .bf_sales {
              float: left;
              width: 49%;
              height: 35px;
              line-height: 35px;
              text-align: center;
              color: #999;
            }
          }
          .bf_goods_foot {
            margin: 2%;
          }
        }
      }
    }
  }
  .bf_footer {
    margin-top: 40px;
    border-top: solid 2px #ff4500;
    background-color: #fff;
    padding: 20px 0;
    .bf_footer_cont {
      text-align: center;
      font-size: 12px;
      width: 100%;
      max-width: 1230px;
      margin: 0 auto;
      .bf_copy {
        margin-top: 10px;
      }
    }
  }
  .active {
    background: #0066ff;
    border-bottom: 1px solid #0066ff;
  }
}
</style>


