<template>
  <div>
    <div class="content-page conn">
      <div class="content">
        <div class="container">
            <div class="row" style="padding:0 10px">
            <div class="navhead">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/admin/index' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item>轮播管理</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12">
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h3 class="panel-title">
                    <font>轮播管理</font>
                    <el-button
                      type="primary"
                      data-toggle="modal"
                      data-target="#myAdd"
                      @click="add"
                      size="mini"
                      style="float:right"
                      round
                    >添加广告轮播</el-button>
                  </h3>
                </div>
                <div class="panel-body">
                  <el-table :data="swiper" v-loading="loading" highlight-current-row style="width: 100%">
                    <el-table-column type="index" width="50"></el-table-column>
                    <el-table-column property="img" label="图片地址" width="350"></el-table-column>
                    <el-table-column property="url" label="跳转地址" width="250"></el-table-column>

                    <el-table-column labelalign="right" label="操作" width="300">
                      <template slot-scope="scope">
                        <el-button
                          size="mini"
                          type="primary"
                          plain
                          @click="edit(scope.$index, scope.row)"
                          data-toggle="modal"
                          data-target="#myEdit"
                        >编辑</el-button>
                        <el-button
                          size="mini"
                          type="danger"
                          @click="swiperDel(scope.$index, scope.row)"
                        >删除</el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                </div>

                <div
                  class="modal fade"
                  :id="flag?'myEdit':'myAdd'"
                  tabindex="-1"
                  role="dialog"
                  aria-labelledby="myModalLabel"
                >
                  <div class="modal-dialog" role="document">
                    <div class="modal-content">
                      <div class="modal-header">
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                          <span aria-hidden="true">&times;</span>
                        </button>
                        <h4 class="modal-title" id="myModalLabel" v-if="flag">编辑广告</h4>
                        <h4 class="modal-title" id="myModalLabel" v-else>添加广告</h4>
                      </div>
                      <div class="modal-body">
                        <div class="form-group">
                          <label for="img">图片地址:</label>
                          <el-input v-model="Info.img" id="img" placeholder="请输入地址"></el-input>
                        </div>
                        <div class="form-group">
                          <label for="url">跳转网址:</label>
                          <el-input v-model="Info.url" id="url" placeholder="请输入网址"></el-input>
                        </div>
                      </div>
                      <div class="modal-footer text-center">
                        <button type="button" class="btn btn-default" data-dismiss="modal">取消</button>
                        <button
                          type="button"
                          class="btn btn-primary"
                          @click="flag?setEdit():setAdd()"
                        >确认</button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import { Toast, Dialog } from "vant";
import qs from "querystring";
export default {
  data() {
    return {
      swiper: [],
      flag: true,
      Info: [],
      loading:true
    };
  },

  mounted() {
    this._getSwiper();
  },
  methods: {
    add() {
      this.flag = false;
      this.Info =[]
    },
    //添加
    setAdd(){
        this.$axios({
        url: api.swiper,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          add: true,
          img: this.Info.img,
          url: this.Info.url
        })
      }).then(res => {
        if (res.data.status == 200) {
          this.$message({
            message: res.data.message,
            type: "success"
          });
        } else {
          this.$message.error(res.data.message);
        }
        $("#myAdd").modal("hide");
        this._getSwiper();
        this.Info =[]
      });
    },
    // 提交编辑
    setEdit() {
      this.$axios({
        url: api.swiper,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          edit: this.Info.id,
          img: this.Info.img,
          url: this.Info.url
        })
      }).then(res => {
        if (res.data.status == 200) {
          this.$message({
            message: res.data.message,
            type: "success"
          });
        } else {
          this.$message.error(res.data.message);
        }
        $("#myEdit").modal("hide");
        this._getSwiper();
        this.Info =[]
      });
    },
    // 获取单个信息
    edit(index, row) {
      this.flag = true;
      this.$axios({
        url: api.swiper,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          id: row.id
        })
      }).then(res => {
        this.Info = res.data.data;
      });
    },
    //  删除
    swiperDel(index, row) {
      Dialog.confirm({
        title: "确认删除",
        message: "删除后无法恢复，是否删除?"
      })
        .then(() => {
          this.$axios({
            url: api.swiper,
            method: "post",
            data: qs.stringify({
              username: sessionStorage.getItem("adminuser"),
              password: sessionStorage.getItem("adminpass"),
              del: row.id
            })
          }).then(res => {
            if (res.data.status == 200) {
              this.$message({
                message: res.data.message,
                type: "success"
              });
            } else {
              this.$message.error(res.data.message);
            }
            this._getSwiper();
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    //获取轮播
    _getSwiper() {
      this.$axios({
        url: api.swiper,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          all: true
        })
      }).then(res => {
        this.swiper = res.data.data;
        this.loading = false
      });
    }
  }
};
</script>


<style scoped>
.el-breadcrumb {
  line-height: 40px;
}
</style>


