<template>
  <div>
    <div class="content-page conn">
      <div class="content">
        <div class="container">
          <div class="row" style="padding:0 10px">
            <div class="navhead">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/admin/index' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item>添加卡密</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12">
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h3 class="panel-title">
                    <font>添加卡密</font>
                  </h3>
                </div>
                <div class="panel-body">
                  <el-form :model="ruleForm" label-width="100px" class="demo-ruleForm">
                    <el-form-item label="选择分类">
                      <el-select v-model="type" placeholder="请选择">
                        <el-option
                          v-for="item in typeList"
                          :key="item.id"
                          :label="item.name"
                          :value="item.id"
                        ></el-option>
                      </el-select>
                    </el-form-item>

                    <el-form-item label="选择商品" v-if="type">
                      <el-select v-model="ruleForm.goodsid" placeholder="请选择">
                        <el-option
                          v-for="item in goods"
                          :key="item.id"
                          :label="item.name"
                          :value="item.id"
                        ></el-option>
                      </el-select>
                    </el-form-item>

                    <el-form-item label="卡密列表" prop="info">
                      <el-input
                        type="textarea"
                        :rows="5"
                        placeholder="请输入卡密，一行一张卡"
                        v-model="ruleForm.info"
                      ></el-input>
                    </el-form-item>

                    <el-form-item label="是否去重" prop="removal">
                      <el-switch
                        v-model="ruleForm.removal"
                        active-color="#13ce66"
                        inactive-color="#ccc"
                        active-value="1"
                        inactive-value="0"
                      ></el-switch>
                      <p style="color:#ccc;font-size:12px">* 开启则不添加相同卡密</p>
                    </el-form-item>

                    <el-form-item>
                      <el-button type="primary" @click="addkm">立即添加</el-button>
                    </el-form-item>
                  </el-form>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import { Toast, Dialog } from "vant";
import qs from "querystring";
export default {
  data() {
    return {
      type: "",
      ruleForm: {
        goodsid: "",
        info: "",
        removal:""
      },
      goods: [],
      goodsList: [],
      typeList: []
    };
  },
  mounted() {
    this._getType();
    this._getGood();
  },
  methods: {
    //添加卡密
    addkm() {
      if (!this.ruleForm.goodsid) {
        this.$message({
          message: "请选择要添加卡密的商品",
          type: "warning"
        });
      } else if (!this.ruleForm.info) {
        this.$message({
          message: "请添加卡密",
          type: "warning"
        });
      }  else {
        this.$axios({
          url: api.km,
          method: "post",
          data: qs.stringify({
            username: sessionStorage.getItem("adminuser"),
            password: sessionStorage.getItem("adminpass"),
            add: this.ruleForm.goodsid,
            info: this.ruleForm.info,
            removal:this.ruleForm.removal
          })
        }).then(res => {
          if (res.data.status == 200) {
            this.$message({
              message: res.data.message,
              type: "success"
            });
            this.ruleForm.info = "";
          } else {
            this.$message.error(res.data.message);
          }
        });
      }
    },

    //获取商品
    _getGood() {
      this.$axios({
        url: api.goods,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          all: true
        })
      }).then(res => {
        this.goodsList = res.data.data;
      });
    },
    //获取分类
    _getType() {
      this.$axios({
        url: api.type,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          all: true
        })
      }).then(res => {
        this.typeList = res.data.data;
      });
    }
  },
  watch: {
    type(newVal) {
      this.ruleForm.goodsid = "";
      this.goods = this.goodsList.filter(function(item, index) {
        return item.type_id == newVal;
      });
    }
  }
};
</script>

<style scoped>
.el-breadcrumb {
  line-height: 40px;
}
</style>


