<template>
  <div class="bf_mobile">
    <div class="bf_head">
      <h1 @click="$router.push('/')">{{data.name}}</h1>
      <el-button type="primary" plain round class="bf_cx" size="medium" @click="kf">联系客服</el-button>
    </div>
    <div class="bf_swipe_bg"></div>
    <div class="bf_query">
      <el-input placeholder="请输入商家订单号/联系方式" v-model="query" class="input-with-select">
        <el-button slot="append" icon="el-icon-search" @click="onQuery"></el-button>
      </el-input>
    </div>
    <!-- <div class="bf_gg" v-if="data.querygg">
      <el-alert :title="data.querygg" type="success"></el-alert>
    </div> -->
    <div class="bf_order">
      <div class="bf_order_title">
        订单记录
        <el-tag>{{order.length}}</el-tag>
      </div>
      <div class="bf_order_log" v-for="(item,index) in order" :key="index">
        <div class="bf_list">订单时间 : {{item.endtime | date}}</div>
        <div class="bf_list">订单编号 : {{item.out_trade_no}}</div>
        <div class="bf_list">商品名称 : {{item.gName}}</div>
        <div class="bf_list">
          卡密内容 :
          <span style="color:red">{{item.km}}</span>
          <button
            v-clipboard:copy="item.km"
            v-clipboard:success="onCopy"
            v-clipboard:error="onError"
            class="bf_copyBtn"
          >点击复制</button>
        </div>
      </div>
    </div>
    <div class="bf_footer">
      <div v-html="data.link" class="bf_link"></div>
      <div v-html="data.copy" class="bf_copy"></div>
    </div>
  </div>
</template>

<script>
import { Toast, Dialog } from "vant";
import api from "@/api";
import qs from "querystring";
export default {
  data() {
    return {
      query: "",
      order: [],
      data: []
    };
  },
  filters: {
    //时间戳转日期
    date: function(time) {
      var date = new Date(time * 1000); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate() + " ";
      var h = date.getHours() + ":";
      var m = date.getMinutes() + ":";
      var s = date.getSeconds();
      return Y + M + D + h + m + s;
    }
  },
  mounted() {
    this._getSet();
    if (this.$route.query.on) {
      this.query = this.$route.query.on;
      this.onQuery();
    }
  },
  methods: {
    //联系客服
    kf() {
      Dialog.confirm({
        // title: '标题',
        message: "客服QQ: " + this.data.kf + " 点击确定联系"
      })
        .then(() => {
          window.location.href =
            "http://wpa.qq.com/msgrd?v=3&uin=" +
            this.data.kf +
            "&site=qq&menu=yes";
        })
        .catch(() => {
          // on cancel
        });
    },
    onCopy(e) {
      Toast.success("复制成功");
    },
    onError(e) {
      Toast.fail("复制失败");
    },
    //查询订单
    onQuery() {
      if (!this.query) {
        Toast.fail("请输入订单号或联系方式");
      } else {
        this.$axios({
          url: api.site,
          method: "post",
          data: qs.stringify({
            query: this.query
          })
        }).then(res => {
          if (res.data.status == 200) {
            Toast.loading({
              message: "加载中...",
              forbidClick: true
            });
            this.order = res.data.data;
          } else {
            Toast.fail(res.data.message);
          }
        });
      }
    },

    //获取部分设置
    _getSet() {
      this.$axios({
        url: api.site
      })
        .then(res => {
          this.data = res.data.data;
          //是否弹出公告
          if (this.data.querygg) {
            Dialog.alert({
              message: this.data.querygg
            }).then(() => {});
          }
        })
        .catch(err => {});
    }
  }
};
</script>

<style lang="less" scoped>
@import "../../../assets/css/reset.css";
.bf_mobile {
  width: 100%;
 
  overflow: hidden;
  position: relative;
  background: #fff;
  .bf_head {
    height: 50px;
    background: #fff;
    border-bottom: 1px solid #ccc;
    box-shadow: 1px 1px 5px #888888;
    padding: 0 10px;
    h1 {
      display: inline-block;
      font-size: 20px;
      line-height: 50px;
    }
    .bf_cx {
      float: right;
      margin-top: 7px;
    }
  }
  .bf_swipe_bg {
    background-image: linear-gradient(0deg, #3399ff, #6633ff 50%);
    position: absolute;
    top: 50px;
    left: -25%;
    height: 140px;
    width: 150%;
    border-bottom-left-radius: 100%;
    border-bottom-right-radius: 100%;
  }
  .bf_query {
    height: 70px;
    padding: 50px 15px 20px 15px;
    .queBtn {
      margin-top: 50px;
    }
  }
  .bf_gg {
    margin: 10px;
  }
  .bf_order {
     min-height: 350px;
    margin: 10px;
    border-radius: 10px;
    border: 1px solid #ccc;
    .el-tag {
      float: right;
      border-radius: 50%;
    }
    .bf_order_title {
      height: 30px;
      font-size: 20px;
      line-height: 30px;
      padding: 5px 10px;
      border-bottom: 0.5px solid #ccc;
    }
    .bf_order_log {
      padding: 5px 10px;
      border-bottom: 0.5px solid #ccc;
      .bf_list {
        height: 26px;
        line-height: 26px;
        font-size: 16px;
        .bf_copyBtn {
          border: 0.5px solid green;
          font-size: 12px;
          border-radius: 10px;
          background: #fff;
          height: 20px;
          line-height: 20px;
        }
      }
    }
  }
  .bf_footer {
    width: 100%;
    padding: 20px 0;
    background: #fff;
    .bf_link {
      text-align: center;
      font-size: 14px;
      margin-top: 10px;
    }
    .bf_copy {
      text-align: center;
      font-size: 14px;
      margin-top: 10px;
      margin-bottom: 10px;
    }
  }
}
</style>


