<template>
  <div>
    <div class="content-page conn">
      <div class="content">
        <div class="container">
          <div class="row" style="padding:0 10px">
            <div class="navhead">
              <el-breadcrumb separator-class="el-icon-arrow-right">
                <el-breadcrumb-item :to="{ path: '/admin/index' }">首页</el-breadcrumb-item>
                <el-breadcrumb-item>订单管理</el-breadcrumb-item>
              </el-breadcrumb>
            </div>
          </div>
          <div class="row">
            <div class="col-md-12">
              <div class="panel panel-default">
                <div class="panel-heading">
                  <h3 class="panel-title">
                    <font>订单管理</font>
                    <el-button
                      type="danger"
                      @click="allDel"
                      size="mini"
                      style="float:right"
                      round
                    >删除无效订单</el-button>
                  </h3>
                </div>
                <div class="panel-body">
                  <el-table
                    v-loading="loading"
                    :data="orderList.filter(data => !search || data.out_trade_no.toLowerCase().includes(search.toLowerCase()))"
                    highlight-current-row
                    style="width: 100%"
                  >
                    <el-table-column type="index" width="50"></el-table-column>
                    <el-table-column property="out_trade_no" label="商户单号" width="180"></el-table-column>
                    <el-table-column property="trade_no" label="交易单号" width="180"></el-table-column>
                    <el-table-column property="money" label="金额" width="60"></el-table-column>
                    <el-table-column property="user" label="购买用户" width="100"></el-table-column>
                    <el-table-column property="number" label="数量" width="60"></el-table-column>
                    <el-table-column property="gName" label="商品名称" width="100"></el-table-column>
                    <el-table-column label="提交时间" width="160">
                      <template slot-scope="scope">
                        <span>{{ scope.row.startTime | date }}</span>
                      </template>
                    </el-table-column>
                    <el-table-column label="付款时间" width="160">
                      <template slot-scope="scope">
                        <span v-if="scope.row.endTime">{{ scope.row.endTime | date }}</span>
                      </template>
                    </el-table-column>
                    <el-table-column label="状态" width="80">
                      <template slot-scope="scope">
                        <span
                          v-if="scope.row.status==1"
                          style="color:green"
                        >{{ scope.row.status | type }}</span>
                        <span v-else style="color:red">{{ scope.row.status | type }}</span>
                      </template>
                    </el-table-column>
                    <el-table-column labelalign="right" width="220">
                      <template slot="header" slot-scope="scope">
                        <el-input v-model="search" size="mini" placeholder="输入商户单号搜索" />
                      </template>
                      <template slot-scope="scope">
                        <el-button
                          size="mini"
                          type="success"
                          @click="getkm(scope.$index, scope.row)"
                        >卡密</el-button>

                        <el-button
                          size="mini"
                          type="danger"
                          @click="orderDel(scope.$index, scope.row)"
                        >删除</el-button>
                      </template>
                    </el-table-column>
                  </el-table>
                </div>

                <!-- 编辑 -->
              </div>
            </div>
          </div>

          <van-dialog
            v-model="kmlist"
            title="卡密信息"
            confirmButtonText="确认"
            cancelButtonText="取消"
            show-cancel-button
          >
            <div class="bf_goBuy">
              <div class="bf_pay_list" v-for="(item,index) in km" :key="index">{{item.km}}</div>
            </div>
          </van-dialog>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import api from "@/api";
import { Toast, Dialog } from "vant";
import qs from "querystring";
export default {
  data() {
    return {
      orderList: [],
      loading: true,
      km: [],
      search: "",
      kmlist: false
    };
  },
  filters: {
    //时间戳转日期
    date: function(time) {
      var date = new Date(time * 1000); //时间戳为10位需*1000，时间戳为13位的话不需乘1000
      var Y = date.getFullYear() + "-";
      var M =
        (date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1) + "-";
      var D = date.getDate() + " ";
      var h = date.getHours() + ":";
      var m = date.getMinutes() + ":";
      var s = date.getSeconds();
      return Y + M + D + h + m + s;
    },
    //状态
    type: function(status) {
      if (status == 0) {
        return "失败";
      } else {
        return "成功";
      }
    }
  },
  mounted() {
    this.getOrder();
  },
  methods: {
    //删除无效订单
    allDel() {
      Dialog.confirm({
        title: "确认删除",
        message: "是否删除全部无效订单,删除后无法恢复,是否继续?"
      })
        .then(() => {
          this.$axios({
            url: api.order,
            method: "post",
            data: qs.stringify({
              username: sessionStorage.getItem("adminuser"),
              password: sessionStorage.getItem("adminpass"),
              alldel: true
            })
          }).then(res => {
            if (res.data.status == 200) {
              this.$message({
                message: res.data.message,
                type: "success"
              });
            } else {
              this.$message.error(res.data.message);
            }
            this.getOrder();
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    // 获取卡密信息
    getkm(index, row) {
      this.$axios({
        url: api.site,
        method: "post",
        data: qs.stringify({
          query: row.out_trade_no
        })
      }).then(res => {
        if (res.data.status == 200) {
          this.km = res.data.data;
          this.kmlist = true;
        } else {
          this.$message.error(res.data.message);
        }
      });
    },
    //  删除单个订单
    orderDel(index, row) {
      Dialog.confirm({
        title: "确认删除",
        message: "删除后无法恢复,是否继续?"
      })
        .then(() => {
          this.$axios({
            url: api.order,
            method: "post",
            data: qs.stringify({
              username: sessionStorage.getItem("adminuser"),
              password: sessionStorage.getItem("adminpass"),
              del: row.id
            })
          }).then(res => {
            if (res.data.status == 200) {
              this.$message({
                message: res.data.message,
                type: "success"
              });
            } else {
              this.$message.error(res.data.message);
            }
            this.getOrder();
          });
        })
        .catch(() => {
          // on cancel
        });
    },
    //获取订单
    getOrder() {
      this.$axios({
        url: api.order,
        method: "post",
        data: qs.stringify({
          username: sessionStorage.getItem("adminuser"),
          password: sessionStorage.getItem("adminpass"),
          all: true
        })
      }).then(res => {
        this.orderList = res.data.data.reverse();
        this.loading = false;
      });
    }
  }
};
</script>


<style scoped>
.bf_goBuy {
  padding: 10px 20px;
}
.bf_pay_list {
  height: 30px;
  line-height: 30px;
}
.el-breadcrumb {
  line-height: 40px;
}
</style>


